import React, { ReactNode } from 'react';
import { Crossed, useVeryCleverSub } from './VeryClever-store';
import { VeryCleverLogic } from './VeryClever-logic';
import { Icon } from '../../components/icons/icon';
import { Util } from '../../util/util';

export type AvailableBonusProps = { children: ReactNode; crossed: Crossed; onClick: () => void };

const AvailableBonus: React.FC<AvailableBonusProps> = ({ children, crossed, onClick }) => (
    <div className="grid-bonus-row">
        <div className="grid-item">{children}</div>
        {crossed.map((c, key) => (
            <div key={key} className="grid-item" onClick={onClick}>
                <div className={Util.classNames('box', c && 'crossed')} />
            </div>
        ))}
    </div>
);

export const AvailableReshuffleBonus: React.FC = () => {
    const { reshuffle } = useVeryCleverSub(({ reshuffle }) => ({ reshuffle }));

    return (
        <AvailableBonus crossed={reshuffle} onClick={VeryCleverLogic.takeReshuffleBonus}>
            <Icon.Refresh2 />
        </AvailableBonus>
    );
};

export const AvailablePlusOneBonus: React.FC = () => {
    const { plusOne } = useVeryCleverSub(({ plusOne }) => ({ plusOne }));

    return (
        <AvailableBonus crossed={plusOne} onClick={VeryCleverLogic.takePlusOneBonus}>
            <div className="content">+1</div>
        </AvailableBonus>
    );
};
