import React, { ReactNode } from 'react';
import { Bonus } from './Bonus';
import { Util } from '../../util/util';
import { Box } from './Box';
import { BONUS, Color } from './data';

type BoxRowProps = {
    color: Exclude<Color, Color.BLACK>;
    contents: ReactNode[];
    className?: string;
    gePrefix?: boolean;
    bonusList: Array<BONUS | undefined>;
    points?: number[];
};

export const BoxRow: React.FC<BoxRowProps> = ({ color, contents, className, gePrefix, bonusList, points = [] }) => (
    <div className={Util.classNames('grid-row', className)} style={{ borderColor: color }}>
        {contents.map((content, index) => {
            const point = points[index];
            const bonus = bonusList[index];
            return (
                <div key={index} className="grid-item">
                    <Box content={content} color={color} gePrefix={gePrefix} index={index} />
                    {point && (
                        <div className="points" style={{ backgroundColor: color }}>
                            {point}
                        </div>
                    )}
                    {bonus && <Bonus bonus={bonus} />}
                </div>
            );
        })}
    </div>
);
