import { Crossed, GameState, Points, VeryCleverStore } from './VeryClever-store';
import { Util } from '../../util/util';
import { BONUS, Color, Data } from './data';

const _takeBonus = (list: Crossed): Crossed => {
    const firstFalse = list.findIndex((v) => !v);
    return firstFalse >= 0 ? Util.nextArray(list, firstFalse, true) : list;
};

const takeReshuffleBonus = () => VeryCleverStore.set(({ reshuffle }) => ({ reshuffle: _takeBonus(reshuffle) }));
const takePlusOneBonus = () => VeryCleverStore.set(({ plusOne }) => ({ plusOne: _takeBonus(plusOne) }));

const _allCrossed = (crossed: Crossed, ...nums: number[]): boolean => nums.every((num) => crossed[num]);

const _getYellowBonus = (crossed: Crossed, earned: BONUS[]): BONUS[] => {
    const collected = [];

    if (!earned.includes(BONUS.YELLOW_10) && _allCrossed(crossed, 0, 4, 8)) {
        collected.push(BONUS.YELLOW_10);
    }
    if (!earned.includes(BONUS.YELLOW_14) && _allCrossed(crossed, 1, 5, 13)) {
        collected.push(BONUS.YELLOW_14);
    }
    if (!earned.includes(BONUS.YELLOW_16) && _allCrossed(crossed, 2, 10, 14)) {
        collected.push(BONUS.YELLOW_16);
    }
    if (!earned.includes(BONUS.YELLOW_20) && _allCrossed(crossed, 7, 11, 15)) {
        collected.push(BONUS.YELLOW_20);
    }
    if (!earned.includes(BONUS.BLUE) && _allCrossed(crossed, 0, 1, 2)) {
        collected.push(BONUS.BLUE);
    }
    if (!earned.includes(BONUS.ORANGE_4) && _allCrossed(crossed, 4, 5, 7)) {
        collected.push(BONUS.ORANGE_4);
    }
    if (!earned.includes(BONUS.GREEN) && _allCrossed(crossed, 8, 10, 11)) {
        collected.push(BONUS.GREEN);
    }
    if (!earned.includes(BONUS.FOX) && _allCrossed(crossed, 13, 14, 15)) {
        collected.push(BONUS.FOX);
    }
    if (!earned.includes(BONUS.PLUS_1) && _allCrossed(crossed, 0, 5, 10, 15)) {
        collected.push(BONUS.PLUS_1);
    }

    return collected;
};

const _getBlueBonus = (crossed: Crossed, earned: BONUS[]): BONUS[] => {
    const collected = [];

    if (!earned.includes(BONUS.REFRESH) && _allCrossed(crossed, 4, 8)) {
        collected.push(BONUS.REFRESH);
    }
    if (!earned.includes(BONUS.GREEN) && _allCrossed(crossed, 1, 5, 9)) {
        collected.push(BONUS.GREEN);
    }
    if (!earned.includes(BONUS.PURPLE) && _allCrossed(crossed, 2, 6, 10)) {
        collected.push(BONUS.PURPLE);
    }
    if (!earned.includes(BONUS.PLUS_1) && _allCrossed(crossed, 3, 7, 11)) {
        collected.push(BONUS.PLUS_1);
    }
    if (!earned.includes(BONUS.ORANGE_5) && _allCrossed(crossed, 1, 2, 3)) {
        collected.push(BONUS.ORANGE_5);
    }
    if (!earned.includes(BONUS.YELLOW) && _allCrossed(crossed, 4, 5, 6, 7)) {
        collected.push(BONUS.YELLOW);
    }
    if (!earned.includes(BONUS.FOX) && _allCrossed(crossed, 8, 9, 10, 11)) {
        collected.push(BONUS.FOX);
    }

    return collected;
};

const _getRowBonus = (color: Color.GREEN | Color.ORANGE | Color.PURPLE, crossed: Crossed, index: number): BONUS[] => {
    const bonus = Data[color].bonus[index];
    return bonus ? [bonus] : [];
};

const _getBonus = (
    color: Color.YELLOW | Color.BLUE | Color.GREEN,
    crossed: Crossed,
    earned: BONUS[],
    index: number
): BONUS[] => {
    if (color === Color.YELLOW) return _getYellowBonus(crossed, earned);
    if (color === Color.BLUE) return _getBlueBonus(crossed, earned);
    if (color === Color.GREEN) return _getRowBonus(Color.GREEN, crossed, index);

    return undefined as never;
};

const _crossableClicked = (color: Color.YELLOW | Color.BLUE | Color.GREEN, index: number, rest?: BONUS[]) =>
    VeryCleverStore.set((state) => {
        const crossed = Util.nextArray(state[color], index, true);
        const earned = color === Color.GREEN ? [] : state.earnedBonus[color];
        const collected = _getBonus(color, crossed, earned, index);

        return {
            [color]: crossed,
            earnedBonus: {
                ...state.earnedBonus,
                [color]: [...earned, ...collected],
            },
            bonus: [...(rest || state.bonus), ...collected],
            highlight: { color, index },
        };
    });

const _getNextIndex = (color: Color.GREEN | Color.ORANGE | Color.PURPLE): number =>
    VeryCleverStore.get()[color].findIndex((v: any) => !v);

const _isCrossed = (color: Exclude<Color, Color.BLACK>, index: number): boolean =>
    !!VeryCleverStore.get()[color][index];

const _isNextIndex = (color: Color.GREEN | Color.ORANGE | Color.PURPLE, index: number): boolean =>
    _getNextIndex(color) === index;

const boxClicked = (color: Exclude<Color, Color.BLACK>, index: number) => {
    if (_isCrossed(color, index)) return;
    if (color === Color.ORANGE || color === Color.PURPLE || color === Color.GREEN) {
        if (!_isNextIndex(color, index)) return;
        if (color !== Color.GREEN) return VeryCleverStore.set({ selection: color });
    }
    return _crossableClicked(color, index);
};

const clearSelection = () => VeryCleverStore.set({ selection: null });

const _selectValue = (color: Color.ORANGE | Color.PURPLE, value: number, rest?: BONUS[]) => {
    const index = _getNextIndex(color);
    const factor = color === Color.ORANGE ? Data[Color.ORANGE].factors[index] : 1;
    VeryCleverStore.set((state) => {
        const values = Util.nextArray(state[color], index, factor * value);
        return {
            [color]: values,
            selection: null,
            bonus: [...(rest || state.bonus), ..._getRowBonus(color, values.map(Boolean), index)],
            highlight: { color: color, index },
        };
    });
};

const selectValue = (value: number) => {
    const { selection } = VeryCleverStore.get();
    if (selection) {
        _selectValue(selection, value);
    }
};

const _yellowPoints = (earned: BONUS[]) => {
    const column1Points = earned.includes(BONUS.YELLOW_10) ? 10 : 0;
    const column2Points = earned.includes(BONUS.YELLOW_14) ? 14 : 0;
    const column3Points = earned.includes(BONUS.YELLOW_16) ? 16 : 0;
    const column4Points = earned.includes(BONUS.YELLOW_20) ? 20 : 0;
    return column1Points + column2Points + column3Points + column4Points;
};
const _bluePoints = (crossed: Crossed) => Data[Color.BLUE].points[crossed.filter(Boolean).length - 1];
const _greenPoints = (crossed: Crossed): number | undefined =>
    Data[Color.GREEN].points[crossed.filter(Boolean).length - 1];
const _sumPoints = (crossed: Points) => crossed.reduce((cur: number, red: number) => cur + red, 0);

const points = (state: GameState) => {
    const yPoints = _yellowPoints(state.earnedBonus[Color.YELLOW]);
    const bPoints = _bluePoints(state[Color.BLUE]);
    const gPoints = _greenPoints(state[Color.GREEN]) || 0;
    const oPoints = _sumPoints(state[Color.ORANGE]);
    const pPoints = _sumPoints(state[Color.PURPLE]);
    const foxPoints = Math.min(yPoints, bPoints, gPoints, oPoints, pPoints) * state.foxes;
    return yPoints + bPoints + gPoints + oPoints + pPoints + foxPoints;
};

const _useBonus = (bonus: BONUS, rest: BONUS[]) => {
    if (bonus === BONUS.GREEN) _crossableClicked(Color.GREEN, _getNextIndex(Color.GREEN), rest);
    else if (bonus === BONUS.ORANGE_4) _selectValue(Color.ORANGE, 4, rest);
    else if (bonus === BONUS.ORANGE_5) _selectValue(Color.ORANGE, 5, rest);
    else if (bonus === BONUS.ORANGE_6) _selectValue(Color.ORANGE, 6, rest);
    else if (bonus === BONUS.PURPLE) _selectValue(Color.PURPLE, 6, rest);
    else if (bonus === BONUS.FOX) VeryCleverStore.set(({ foxes }) => ({ foxes: foxes + 1, bonus: rest }));
    else if (bonus === BONUS.PLUS_1)
        VeryCleverStore.set(({ plusOne }) => ({ plusOne: [...plusOne, false], bonus: rest }));
    else if (bonus === BONUS.REFRESH)
        VeryCleverStore.set(({ reshuffle }) => ({ reshuffle: [...reshuffle, false], bonus: rest }));
    else VeryCleverStore.set({ bonus: rest });
};

const useBonus = () => {
    const { bonus } = VeryCleverStore.get();
    if (bonus) {
        const [first, ...rest] = bonus;
        _useBonus(first, rest);
    }
};

const ROUND_BONUS = [BONUS.REFRESH, BONUS.PLUS_1, BONUS.REFRESH, BONUS.ULTIMATE];
const _getRoundBonus = (round: number) => (ROUND_BONUS[round] ? [ROUND_BONUS[round]] : []);

const nextRound = () =>
    VeryCleverStore.set(({ round }) => ({
        round: round + 1,
        bonus: _getRoundBonus(round),
    }));

export const VeryCleverLogic = {
    takeReshuffleBonus,
    takePlusOneBonus,
    boxClicked,
    selectValue,
    clearSelection,
    points,
    useBonus,
    nextRound,
};
