import React, { ReactNode } from 'react';
import { AgainStore } from './again-store';
import { Util } from '../../util/util';

const BoxWrapper: React.FC<{ children: ReactNode; onClick?: () => void }> = ({ children, onClick }) => (
    <div>
        <div className="box" onClick={onClick}>
            {children}
        </div>
    </div>
);

const NumberJoker: React.FC = () => (
    <BoxWrapper>
        <svg viewBox="0 0 50 50">
            <rect x="0" y="0" width="50" height="50" rx="10" ry="10" fill="black" />
            <text textAnchor="middle" x="25" y="38" fontSize="40" fontWeight="900" fill="white">
                ?
            </text>
        </svg>
    </BoxWrapper>
);

const ColorJoker: React.FC = () => (
    <BoxWrapper>
        <svg viewBox="0 0 50 50">
            <rect x="0" y="0" width="50" height="50" rx="10" ry="10" fill="black" />
            <g stroke="white" strokeWidth="10">
                <line x1="10" y1="10" x2="40" y2="40" />
                <line x1="10" y1="40" x2="40" y2="10" />
            </g>
        </svg>
    </BoxWrapper>
);

const JokerSeparator: React.FC = () => (
    <BoxWrapper>
        <svg viewBox="0 0 50 50">
            <line x1="15" y1="45" x2="35" y2="5" stroke="black" strokeWidth="10" />
        </svg>
    </BoxWrapper>
);

const EqualSign: React.FC = () => (
    <BoxWrapper>
        <svg viewBox="0 0 50 50">
            <line x1="10" y1="20" x2="40" y2="20" stroke="black" strokeWidth="7" />
            <line x1="10" y1="30" x2="40" y2="30" stroke="black" strokeWidth="7" />
        </svg>
    </BoxWrapper>
);

export const JokerPlaceholder: React.FC<{ taken: boolean; onClick?: () => void }> = ({ taken, onClick }) => (
    <BoxWrapper onClick={onClick}>
        <svg viewBox="0 0 50 50">
            <circle cx="25" cy="25" r="23" fill="white" stroke="black" strokeWidth="2" />
            <text textAnchor="middle" x="25" y="38" fontSize="40" fontWeight="900">
                !
            </text>
            {taken && (
                <g stroke="black" strokeWidth="4">
                    <line x1="7" y1="7" x2="43" y2="43" />
                    <line x1="7" y1="43" x2="43" y2="7" />
                </g>
            )}
        </svg>
    </BoxWrapper>
);

type JokersProps = { takenJokers: number };
export const Jokers: React.FC<JokersProps> = ({ takenJokers }) => (
    <div className="again-jokers">
        <NumberJoker />
        <JokerSeparator />
        <ColorJoker />
        <EqualSign />
        {Util.array(8).map((_, index) => (
            <JokerPlaceholder
                onClick={index === takenJokers ? AgainStore.takeJoker : undefined}
                key={index}
                taken={index < takenJokers}
            />
        ))}
    </div>
);
