import { Persistore } from 'persistore';
import { createStore } from 'react-use-sub';

export type CodePoint = 1 | 2 | 3 | 4;
export type Code = [CodePoint, CodePoint, CodePoint];
export type CodeWords = { first: string; second: string; third: string };
export type Results = { losses: number; wins: number };
export type Knowledge = [string[], string[], string[], string[]];
const NoKnowledge: Knowledge = [[], [], [], []];
export type PlayerState = { knowledge: Knowledge; code?: Code; guessed?: Code; codeWords?: CodeWords; round: number };

export type DecryptoState = { results: Results; me: PlayerState; enemy: PlayerState };
const initialDecryptoState: DecryptoState = {
    results: { losses: 0, wins: 0 },
    me: { knowledge: NoKnowledge, round: 0 },
    enemy: { knowledge: NoKnowledge, round: 0 },
};

const _key = 'decrypto';

const _fetch = (): DecryptoState => {
    try {
        const raw = Persistore.get(_key);
        if (raw) {
            return JSON.parse(raw);
        }
    } catch (e) {
        // do nothing
    }
    return initialDecryptoState;
};
const _persist = (state: DecryptoState) => Persistore.set(_key, JSON.stringify(state));

const [useDecryptoSub, Store] = createStore<DecryptoState>(_fetch());
export { useDecryptoSub };

const set: typeof Store['set'] = (update) => {
    Store.set(update);
    _persist(Store.get());
};

const reset = () => set(initialDecryptoState);

export const DecryptoStore = { set, reset };
