import React, { ReactNode, useCallback } from 'react';
import { Highlight, useVeryCleverSub } from './VeryClever-store';
import { Util } from '../../util/util';
import { VeryCleverLogic } from './VeryClever-logic';
import { Color } from './data';

const _isHighLight = (highlight: null | Highlight, color: Color, index: number): boolean => {
    if (!highlight) return false;
    else return highlight.color === color && highlight.index === index;
};

type BoxProps = { content: ReactNode; color: Exclude<Color, Color.BLACK>; gePrefix?: boolean; index: number };

export const Box = ({ content, color, gePrefix, index }: BoxProps) => {
    const { crossed, newContent, highlight } = useVeryCleverSub((state) => {
        const highlight = _isHighLight(state.highlight, color, index);
        switch (color) {
            case Color.ORANGE:
            case Color.PURPLE:
                return { crossed: false, newContent: state[color][index], highlight };
            default:
                return { crossed: state[color][index], highlight };
        }
    });

    const onClick = useCallback(() => {
        VeryCleverLogic.boxClicked(color, index);
    }, [color, index]);

    return (
        <div
            onClick={onClick}
            className={Util.classNames('box', crossed && 'crossed', highlight && 'highlight')}
            style={{ color, borderColor: color }}>
            {gePrefix ? (
                <div className="content">
                    &ge;
                    {content}
                </div>
            ) : newContent ? (
                <div className="picked">{newContent}</div>
            ) : (
                <div className="content">{content}</div>
            )}
        </div>
    );
};
