import React, { useMemo } from 'react';
import { Knowledge } from './Decrypto-store';

export type KnowledgeViewProps = { knowledge: Knowledge };

export const KnowledgeView: React.FC<KnowledgeViewProps> = ({ knowledge }) => {
    const rows = useMemo(() => {
        const result = [];
        const max = knowledge.reduce((red, cur) => Math.max(red, cur.length), 0);
        for (let i = 0; i < max; i++) {
            result.push(
                <tr key={i}>
                    {knowledge.map((k, idx) => (
                        <td key={idx}>{k[i]}</td>
                    ))}
                </tr>
            );
        }
        return result;
    }, [knowledge]);

    return (
        <div className="knowledge">
            <table>
                <thead>
                    <tr>
                        <td>1</td>
                        <td>2</td>
                        <td>3</td>
                        <td>4</td>
                    </tr>
                </thead>
                <tbody>{rows}</tbody>
            </table>
        </div>
    );
};
