import { useWindowResizeEffect } from '../../hooks/useWindowResizeEffect';
import { useEffect } from 'react';

const OPTIMAL_HEIGHT_WIDTH_RATIO = 0.48;
export const RESIZE_CLASS = 'again-resize';

const calculateNecessaryPadding = (): number => {
    const ratioDiscrepancy = OPTIMAL_HEIGHT_WIDTH_RATIO - (window.innerHeight - 48) / window.innerWidth;
    return ratioDiscrepancy > 0 ? ratioDiscrepancy * window.innerWidth : 0;
};

const applyPadding = () => {
    const padding = calculateNecessaryPadding();
    const paddingValue = padding ? `0 ${padding}px` : '';
    Array.from(document.getElementsByClassName(RESIZE_CLASS)).forEach((elem) => {
        (elem as HTMLElement).style.setProperty('padding', paddingValue);
    });
};

export const useAgainResize = () => {
    useEffect(applyPadding, []);
    useWindowResizeEffect(applyPadding);
};
