import React from 'react';
import { Cube } from './Cube';
import { Icon } from '../../components/icons/icon';
import { Box } from './Box';
import { allLetters } from './Letters';
import { AgainStore, Chosen, ChosenTurn, TurnState, TurnType } from './again-store';
import { __ } from 'first-intl';
import { Footer, FooterButton } from '../../footer/Footer';
import { JokerPlaceholder } from './Jokers';

export const Abort = () => (
    <svg className="icon" viewBox="0 0 500 500">
        <g stroke="red" strokeWidth="100">
            <line x1="80" y1="80" x2="420" y2="420" />
            <line x1="80" y1="420" x2="420" y2="80" />
        </g>
    </svg>
);

export const Finish = () => (
    <svg className="icon" viewBox="0 0 500 500">
        <g stroke="darkgreen" strokeWidth="100">
            <line x1="70" y1="180" x2="262" y2="347" />
            <line x1="220" y1="320" x2="420" y2="80" />
        </g>
    </svg>
);

const SkipTurn: React.FC = () => (
    <>
        <Icon.Ban className="cube" />
    </>
);

type ChosenTurnDisplayProps = { chosen: ChosenTurn };
const ChosenTurnDisplay: React.FC<ChosenTurnDisplayProps> = ({ chosen: { count, color } }) => (
    <>
        <Cube count={count} />
        <Cube color={color} />
    </>
);

type FooterTurnControlProps = { chosen: Chosen };
export const FooterTurnControl: React.FC<FooterTurnControlProps> = ({ chosen }) => (
    <Footer hideHome className="turn-control top">
        <FooterButton onClick={AgainStore.onAbort}>
            <Abort />
        </FooterButton>
        <FooterButton className="turn">
            <ChosenTurnDisplay chosen={chosen} />
        </FooterButton>
        <FooterButton onClick={AgainStore.onFinish}>
            <Finish />
        </FooterButton>
    </Footer>
);

const Success: React.FC<{ chosen: ChosenTurn }> = ({ chosen: { completedColor, completedRows } }) => {
    const completed = [];
    if (completedColor) completed.push(<Cube color={completedColor} key="color" />);
    if (completedRows)
        completedRows.forEach((isCompleted, index) => {
            if (isCompleted) completed.push(<Box content={allLetters[index]} key={index} />);
        });
    return completed.length > 0 ? (
        <>
            <Icon.ArrowRight className="success-arrow" />
            {completed}
        </>
    ) : null;
};

export const Turn: React.FC<{ turn: TurnState; hideIndex?: boolean }> = ({ turn, hideIndex }) => {
    switch (turn.type) {
        case TurnType.CHOSEN:
            return (
                <div className="again-turn">
                    {hideIndex || `${turn.index}) `}
                    <div className="turn-chosen">
                        <ChosenTurnDisplay chosen={turn.data} />
                    </div>
                    {hideIndex || (
                        <div className="turn-success">
                            <Success chosen={turn.data} />
                        </div>
                    )}
                </div>
            );
        case TurnType.SKIP:
            return (
                <div className="again-turn">
                    {hideIndex || `${turn.index}) `}
                    <div className="turn-chosen">
                        <SkipTurn />
                    </div>
                </div>
            );
        case TurnType.JOKER:
            return (
                <div className="again-pseudo-turn">
                    <JokerPlaceholder taken />
                </div>
            );
        case TurnType.COLOR:
            return (
                <div className="again-pseudo-turn">
                    <Box color={turn.data} crossed />
                </div>
            );
        case TurnType.ROW:
            return (
                <div className="again-pseudo-turn">
                    <Box content={turn.data} crossed />
                </div>
            );
    }
};

type TurnsProps = { turns: TurnState[] };
export const Turns = ({ turns }: TurnsProps) => (
    <div className="again-turns">
        <h4>{__('AGAIN.madeTurns')}</h4>
        {turns.map((turn, index) => (
            <Turn turn={turn} key={index} />
        ))}
    </div>
);
