import React, { useCallback } from 'react';
import { Code, DecryptoStore, PlayerState, useDecryptoSub } from './Decrypto-store';
import { GuessCode } from './GuessCode';
import { GuessResult } from './GuessResult';
import { KnowledgeView } from './KnowledgeView';
import { openQrCodeScannerOverlay } from './QrCodeOverlay';
import { Icon } from '../../components/icons/icon';
import { __ } from 'first-intl';

const _setEnemy = (meUpdates: Partial<PlayerState>) =>
    DecryptoStore.set(({ enemy }) => ({ enemy: { ...enemy, ...meUpdates } }));
const _setGuessed = (guessed: Code) =>
    DecryptoStore.set(({ enemy, results: { wins, losses } }) => {
        const { codeWords, code } = enemy;
        const { first, second, third } = codeWords!;
        const correctGuess = guessed[0] === code![0] && guessed[1] === code![1] && guessed[2] === code![2];
        const knowledge: any = [...enemy.knowledge].map((arr) => [...arr]);
        knowledge[code![0] - 1].push(first);
        knowledge[code![1] - 1].push(second);
        knowledge[code![2] - 1].push(third);
        const updatedEnemy = { enemy: { ...enemy, knowledge, guessed } };
        return correctGuess ? { ...updatedEnemy, results: { wins: wins + 1, losses } } : updatedEnemy;
    });

export const DecryptoEnemy: React.FC = () => {
    const { code, codeWords, guessed, knowledge, round } = useDecryptoSub(({ enemy }) => enemy);
    const disableCatch = useDecryptoSub(({ me: { round, codeWords }, enemy, results: { wins, losses } }) => {
        const readyMeRound = codeWords ? round : round - 1;
        return readyMeRound <= enemy.round;
    });

    const onClick = useCallback(() => {
        _setEnemy({ code: undefined, codeWords: undefined, guessed: undefined });
        openQrCodeScannerOverlay();
    }, []);

    const noCodeOrFinishedGuessing = !code || !!(code && codeWords && guessed);

    return (
        <>
            {round > 0 && (
                <div className="round">
                    {__('DECRYPTO.round')} {round}
                </div>
            )}
            {noCodeOrFinishedGuessing && (
                <div className="scan-code">
                    <button onClick={onClick} type="button" disabled={disableCatch}>
                        <Icon.Glasses />
                        <div className="description">{__('DECRYPTO.btn.catch')}</div>
                    </button>
                </div>
            )}
            {code && codeWords && !guessed && <GuessCode onSubmit={_setGuessed} codeWords={codeWords} />}
            {code && codeWords && guessed && <GuessResult codeWords={codeWords} code={code} guessed={guessed} />}
            <KnowledgeView knowledge={knowledge} />
        </>
    );
};
