import React from 'react';
import { Box } from './Box';
import { Util } from '../../util/util';
import { RESIZE_CLASS } from './again-resize';

export const allLetters = 'ABCDEFGHIJKLMNO'.split('');

export const Letters: React.FC = () => (
    <div className={Util.classNames('again-letters', RESIZE_CLASS)}>
        {allLetters.map((letter, index) => (
            <Box key={index} content={letter} />
        ))}
    </div>
);
