import React, { useState, useCallback, useEffect, FormEvent } from 'react';
import { Input } from '../../components/form/input';

export type PointsState = { tip?: number; checked?: boolean; earned?: number };

type PointsComponentProps = {
    points: PointsState;
    onChange: (player: PointsState) => void;
    limit: number;
    checkable?: boolean;
    active: boolean;
    activeRound: boolean;
};

const parseNum = (v: string): number | undefined => {
    if (v && v.trim()) {
        const parsed = Number(v.trim());
        if (!isNaN(parsed) && parsed >= 0) {
            return parsed;
        }
    }
};

const _tipToString = (tip?: number) => (tip !== undefined ? String(tip) : '');

export const Points = ({ points, activeRound, limit, onChange, checkable, active }: PointsComponentProps) => {
    const [focus, setFocus] = useState(false);
    const [currentTip, setCurrentTip] = useState<string>(() => _tipToString(points.tip));

    const onFocus = useCallback(() => {
        activeRound && setFocus(true);
    }, [activeRound]);

    const onChangeTip = useCallback(
        (tip: string) => {
            if (activeRound) {
                const num = parseNum(tip);
                if (num === undefined) {
                    setCurrentTip('');
                } else if (num >= 0 && num <= limit) {
                    setCurrentTip(tip);
                }
            }
        },
        [activeRound, limit]
    );

    const onBlur = useCallback(
        (tip: string) => {
            if (activeRound) {
                const num = parseNum(tip);
                if (num !== undefined) {
                    onChange({ ...points, tip: num });
                }
                setFocus(false);
            }
        },
        [activeRound, points, onChange]
    );

    const onSubmit = useCallback(
        (event: FormEvent) => {
            event.preventDefault();
            onBlur(currentTip);
        },
        [onBlur, currentTip]
    );

    const setEarned = useCallback(
        (earned: number) => {
            if (!activeRound || points.earned) return;
            onChange({ ...points, earned });
        },
        [activeRound, points, onChange]
    );

    const onSuccess = useCallback(() => {
        setEarned(Number(points.tip) + 10);
    }, [setEarned, points]);

    const onFailed = useCallback(() => {
        setEarned(Number(points.tip) * -1);
    }, [setEarned, points]);

    useEffect(() => {
        setCurrentTip(_tipToString(points.tip));
    }, [points.tip]);

    if (points.earned !== undefined) {
        return points.earned > 0 ? (
            <td className="points points-earned">{points.earned}</td>
        ) : (
            <td className="points points-lost">{points.earned}</td>
        );
    }

    if (!focus) {
        const clazzName = activeRound && active ? 'points active' : 'points';
        const readyToPlay = activeRound && checkable;
        return (
            <td onClick={readyToPlay ? undefined : onFocus} className={clazzName}>
                <div onClick={readyToPlay ? onFocus : undefined} className="points-tip">
                    {currentTip}
                </div>
                {readyToPlay && (
                    <>
                        <button className="points-success" type="button" onClick={onSuccess}>
                            +
                        </button>
                        <button className="points-failed" type="button" onClick={onFailed}>
                            -
                        </button>
                    </>
                )}
            </td>
        );
    }
    return (
        <td>
            <form onSubmit={onSubmit}>
                <Input
                    className="points-input"
                    value={currentTip}
                    autoFocus
                    onChange={onChangeTip}
                    onBlur={onBlur}
                    type="number"
                />
            </form>
        </td>
    );
};
