import React from 'react';
import Icons from './icon-path.json';
import { Util } from '../../util/util';

type IconProps = { className?: string };

const _Icon = (symbol: keyof typeof Icons, props: IconProps = {}, width: number = 512) => (
    <svg className={props.className ? props.className + ' icon' : 'icon'} viewBox={`0 0 ${width} 512`}>
        <path fill="currentColor" d={Icons[symbol]} />
    </svg>
);

const ArrowDown = ({ className }: IconProps) => <Icon.ArrowRight className={Util.classNames('rotate-90', className)} />;
const ChevronDown = ({ className }: IconProps) => (
    <Icon.ChevronUp className={Util.classNames('rotate-180', className)} />
);

export const Icon = {
    Alert: (props?: IconProps) => _Icon('ALERT', props, 576),
    ArrowRight: (props?: IconProps) => _Icon('ARROW_RIGHT', props, 448),
    ChevronUp: (props?: IconProps) => _Icon('CHEVRON_UP', props, 448),
    ChevronDown,
    ArrowDown,
    Ban: (props?: IconProps) => _Icon('BAN', props),
    Crown: (props?: IconProps) => _Icon('CROWN', props, 640),
    Exchange: (props?: IconProps) => _Icon('EXCHANGE', props),
    Forward: (props?: IconProps) => _Icon('FORWARD', props),
    Glasses: (props?: IconProps) => _Icon('GLASSES', props, 576),
    Home: (props?: IconProps) => _Icon('HOME', props, 576),
    Key: (props?: IconProps) => _Icon('KEY', props),
    Mask: (props?: IconProps) => _Icon('MASK', props, 640),
    Menu: (props?: IconProps) => _Icon('MENU', props, 448),
    Undo: (props?: IconProps) => _Icon('UNDO', props),
    PowerOff: (props?: IconProps) => _Icon('POWER_OFF', props),
    QrCode: (props?: IconProps) => _Icon('QR_CODE', props, 448),
    Refresh: (props?: IconProps) => _Icon('REFRESH', props, 640),
    Refresh2: (props?: IconProps) => _Icon('REFRESH_2', props),
    Settings: (props?: IconProps) => _Icon('SETTINGS', props),
    Times: (props?: IconProps) => _Icon('TIMES', props),
    Trash: (props?: IconProps) => _Icon('TRASH', props, 448),
};
