import React, { useEffect, useRef, useState } from 'react';
import QRCode from 'qrcode';

type ColorOption = { dark?: string; light?: string };
const transparent = '#0000';
// we always choose transparent bg if any color of the qr code itself was chosen,
// because the bg can be better regulated by the outer css styles
export const QrCodeColorOptions = {
    white: { dark: '#FFF', light: transparent },
    black: { dark: '#000', light: transparent },
};

export type QrCodeOptions = { margin?: number; width?: number; color?: ColorOption };

type QrCodeProps = { info: string; options: QrCodeOptions };

export const QrCode: React.FC<QrCodeProps> = ({ info, options }) => {
    const [error, setError] = useState<Error | undefined>();
    const canvas = useRef<HTMLCanvasElement | null>(null);

    useEffect(() => {
        QRCode.toCanvas(canvas.current, info, options, (error) => {
            if (error) setError(error);
        });
    }, [info, options]);

    if (error) return <>Ups, something went wrong (Code: QR1)</>;

    return <canvas ref={canvas} />;
};
