import React, { ReactNode } from 'react';
import { Icon } from '../../components/icons/icon';
import { BONUS, Color, CROSS } from './data';

export const FOX = '';
export const PLUS_1 = '+1';
export const REFRESH = <Icon.Refresh2 />;

const CONTENT_AND_COLOR: { [bonus in BONUS]: { content: ReactNode; color: Color } } = {
    [BONUS.FOX]: { content: FOX, color: Color.BLACK },
    [BONUS.PLUS_1]: { content: PLUS_1, color: Color.BLACK },
    [BONUS.REFRESH]: { content: REFRESH, color: Color.BLACK },
    [BONUS.YELLOW]: { content: CROSS, color: Color.YELLOW },
    [BONUS.YELLOW_10]: { content: 10, color: Color.YELLOW },
    [BONUS.YELLOW_14]: { content: 14, color: Color.YELLOW },
    [BONUS.YELLOW_16]: { content: 16, color: Color.YELLOW },
    [BONUS.YELLOW_20]: { content: 20, color: Color.YELLOW },
    [BONUS.BLUE]: { content: CROSS, color: Color.BLUE },
    [BONUS.GREEN]: { content: CROSS, color: Color.GREEN },
    [BONUS.ORANGE_4]: { content: 4, color: Color.ORANGE },
    [BONUS.ORANGE_5]: { content: 5, color: Color.ORANGE },
    [BONUS.ORANGE_6]: { content: 6, color: Color.ORANGE },
    [BONUS.PURPLE]: { content: 6, color: Color.PURPLE },
    [BONUS.ULTIMATE]: { content: CROSS + '/6', color: Color.BLACK },
};

type BonusProps = { bonus: BONUS };

export const Bonus: React.FC<BonusProps> = ({ bonus }) => (
    <div className="box bonus" style={{ backgroundColor: CONTENT_AND_COLOR[bonus].color }}>
        <div className="content">{CONTENT_AND_COLOR[bonus].content}</div>
    </div>
);
