import React, { ReactNode, useEffect } from 'react';
import { Abort, Finish } from './Turn';
import { Icon } from '../../components/icons/icon';
import { AgainStore, SplashIconType, useAgainSub } from './again-store';

const renderIcon = (iconType: SplashIconType | null): ReactNode => {
    if (!iconType) return null;
    switch (iconType) {
        case 'cancel':
            return <Abort />;
        case 'submit':
            return <Finish />;
        case 'undo':
            return <Icon.Undo className="reflect" />;
        case 'skip':
            return <Icon.Ban />;
        default:
            return null;
    }
};

const hide = () => AgainStore.setSplash(null);

export const ActionSplash: React.FC = () => {
    const { icon } = useAgainSub(({ againSplash }) => ({ icon: againSplash }));

    useEffect(
        () => () => {
            window.setTimeout(hide, 500);
        },
        [icon]
    );

    const iconView = renderIcon(icon);

    if (!iconView) return null;

    return (
        <div className="again-splash">
            <div className="splash-overlay" />
            <div className="splash-content">{iconView}</div>
        </div>
    );
};
