import React, { useCallback } from 'react';
import { Code, CodeWords } from './Decrypto-store';
import { openQrCodeInfoOverlay } from './QrCodeOverlay';
import { Icon } from '../../components/icons/icon';
import { __ } from 'first-intl';

export type ShowQrCodeProps = { code: Code; codeWords: CodeWords };

export const ShowQrCode: React.FC<ShowQrCodeProps> = ({ codeWords, code }) => {
    const onClick = useCallback(() => openQrCodeInfoOverlay(btoa(JSON.stringify({ code, codeWords }))), [
        code,
        codeWords,
    ]);

    return (
        <button className="open-qr-code" onClick={onClick} type="button">
            <Icon.QrCode />
            <div className="description">{__('DECRYPTO.btn.transfer')}</div>
        </button>
    );
};
