import React, { useCallback, useState } from 'react';

export type ClickableDescriptionProps = { desc: string; short?: string; className?: string; tag?: string };

export const ClickableDescription: React.FC<ClickableDescriptionProps> = ({ desc, short, className, tag = 'div' }) => {
    const [showName, setShowName] = useState(false);
    const displayed = showName ? desc : short || desc;

    const onClick = useCallback(() => {
        if (short) {
            setShowName(true);
            window.setTimeout(() => setShowName(false), 3000);
        }
    }, [short]);

    return React.createElement(tag, { onClick, className }, displayed);
};
