import React, { useCallback, useState } from 'react';
import { Admin } from './Admin';

const _AdminEntry = {
    lastClicked: 0,
    successCount: 0,
    margin: 100,
    timing: [474, 405, 135, 464, 919, 446], // TODO(VL): Rework this entry strategy. Does not work reliably on all devices
};

// TODO(VL): Should be possible everywhere to open the admin panel. Not only on first page
export const AdminEntry: React.FC = () => {
    const [active, setActive] = useState(false);
    const onClick = useCallback(() => {
        const now = Date.now();
        const diff = now - _AdminEntry.lastClicked;
        if (diff > 2000) {
            // console.log('First');
            _AdminEntry.successCount = 0;
        } else {
            const timing = _AdminEntry.timing[_AdminEntry.successCount];
            if (diff - _AdminEntry.margin < timing && timing < diff + _AdminEntry.margin) {
                // console.log('Success');
                if (_AdminEntry.successCount === 5) {
                    _AdminEntry.successCount = 0;
                    setActive(true);
                } else {
                    _AdminEntry.successCount++;
                }
            } else {
                // console.log('Failed');
                _AdminEntry.successCount = 0;
            }
        }
        _AdminEntry.lastClicked = now;
    }, []);

    const close = useCallback(() => setActive(false), []);

    return active ? <Admin close={close} /> : <div className="admin-entry" onClick={onClick} />;
};
