import React, { Component } from 'react';
import { Globals } from '../globals';

const missions = [
    {
        title: 'Save the world',
        text:
            'Did you never think about saving the world? You can do it. Just follow the given instructions...' +
            'You need to modify your attitude. You have to become more optimistic and you should... bla blub foo bar',
    },
    {
        title: 'Marry her',
        text: 'It is finally the time. Get the ring. Make the first step and perform your show to win her heart <3',
    },
    {
        title: 'Punch a bear',
        text:
            "Yes. I'm serious. You should punch some big bear into his stomach. But possible you should first " +
            'think about a good plan to get away again - as fast as possible :{',
    },
];

export class MissionControl extends Component<{}, { processed: number; selected: number }> {
    state = { processed: 0, selected: 0 };
    interval = 0;

    processNextLetter = () => {
        const current = this.state.processed;
        if (current === missions[this.state.selected].text.length) Globals.window().clearInterval(this.interval);
        this.setState({ processed: current + 1 });
    };

    componentDidMount() {
        this.runLetters();
    }

    runLetters = () => (this.interval = Globals.window().setInterval(this.processNextLetter, 50));

    select = (mission: number) => () => {
        Globals.window().clearInterval(this.interval);
        this.setState({ processed: 0, selected: mission });
        this.runLetters();
    };

    componentWillUnmount() {
        Globals.window().clearInterval(this.interval);
    }

    render() {
        return (
            <div className="mission-control">
                <div className="mission-content">
                    <div className="mission-list">
                        <ul>
                            {missions.map((m, i) => {
                                const isSelected = this.state.selected === i;
                                const className = isSelected ? 'selected' : undefined;
                                const handler = isSelected ? undefined : this.select(i);
                                return (
                                    <li key={i} onClick={handler} className={className}>
                                        {m.title}
                                    </li>
                                );
                            })}
                        </ul>
                    </div>
                    <div className="mission-desc">
                        {missions[this.state.selected].text.substring(0, this.state.processed)}
                    </div>
                </div>
            </div>
        );
    }
}
