import { PointsState } from './Points';
import { Persistore } from 'persistore';
import { SpadeIsTrumpStore } from './spadeIsTrump-store';
import { Util } from '../../util/util';

const _key = 'SIT.players';

export type Player = { uuid: string; name: string; shortcut?: string };
export type PlayerState = { player: Player; points: PointsState[]; total: number };
export type GameState = PlayerState[];

const _get = (): Player[] => {
    try {
        const players = Persistore.get(_key);
        return JSON.parse(players as any);
    } catch (e) {
        return [];
    }
};

const _set = (next: Player[]): void => Persistore.set(_key, JSON.stringify(next));

const set = (update: Player[] | ((players: Player[]) => Player[])) => {
    if (typeof update === 'function') {
        SpadeIsTrumpStore.set(({ players }) => ({ players: update(players) }));
    } else {
        SpadeIsTrumpStore.set({ players: update });
    }
    _set(SpadeIsTrumpStore.get().players);
};

const add = (name: string, shortcut?: string) => set((players) => [{ name, shortcut, uuid: Util.uuid() }, ...players]);

const update = (update: Player): void =>
    set((players) => {
        const index = players.findIndex((p) => p.uuid === update.uuid);
        return index === -1 ? players : Util.nextArray(players, index, update);
    });

const remove = (player: Player): void => set((players) => players.filter((p) => p.uuid !== player.uuid));

const moveTo = (before: number, after: number) =>
    set((players) => {
        const next = players.filter((_, index) => index !== before);
        next.splice(after, 0, players[before]);
        return next;
    });

const moveUp = (current: number) => (current > 0 ? moveTo(current, current - 1) : undefined);
const moveDown = (current: number) => moveTo(current, current + 1);

SpadeIsTrumpStore.set({ players: _get() });

export const PlayerStore = { set, add, update, remove, moveTo, moveDown, moveUp };
