import React from 'react';
import { Bonus } from './Bonus';
import { BONUS } from './data';
import { VeryCleverLogic } from './VeryClever-logic';

export type ActivatedBonusProps = { earned: BONUS[] };

export const ActivatedBonus: React.FC<ActivatedBonusProps> = ({ earned }) =>
    earned.length ? (
        <div className="bonus-splash" onClick={VeryCleverLogic.useBonus}>
            <div className="bonus-splash-overlay" />
            <div className="bonus-splash-inner">
                {earned.map((bonus: BONUS, index: number) => (
                    <Bonus bonus={bonus} key={index} />
                ))}
            </div>
        </div>
    ) : null;
