import React, { Fragment, ReactNode, useEffect, useState } from 'react';
import { addIntlData } from 'first-intl';
import deIntl from './intl-de.json';
import enIntl from './intl-en.json';
import { Persistore } from 'persistore';

type SupportedLanguage = 'de' | 'en';

const _key = 'lang';

const _persist = (chosen: SupportedLanguage) => Persistore.set(_key, chosen);
const _getPersisted = (): SupportedLanguage | undefined => {
    const saved = Persistore.get(_key);
    return saved === 'de' || saved === 'en' ? saved : undefined;
};

addIntlData(deIntl);

const _detectPreferred = (): SupportedLanguage => {
    const lang = navigator.language;
    return lang && lang.startsWith('en') ? 'en' : 'de';
};

let currentLang = _getPersisted() || _detectPreferred();

if (currentLang !== 'de') {
    addIntlData(enIntl);
}

const Updater = { setKey: (key: SupportedLanguage) => {} };

export const toggleIntl = () => {
    if (currentLang === 'de') {
        currentLang = 'en';
        addIntlData(enIntl);
        Updater.setKey('en');
        _persist('en');
    } else {
        currentLang = 'de';
        addIntlData(deIntl);
        Updater.setKey('de');
        _persist('de');
    }
};

export const IntlRoot: React.FC<{ children: ReactNode }> = ({ children }) => {
    const [key, setKey] = useState<SupportedLanguage>(currentLang);

    useEffect(() => {
        Updater.setKey = setKey;
    }, []);

    return <Fragment key={key}>{children}</Fragment>;
};

export const getCurrentIntl = (): SupportedLanguage => currentLang;
