import { useRef, useCallback } from 'react';
import { Globals } from '../globals';
import { useEventListener } from './useEventListener';
import { HofUtil } from '../util/hof-util';

type ResizeCallback = (width: number) => void;

export const useWindowResizeEffect = (cb: ResizeCallback) => {
    const callbackRef = useRef<ResizeCallback>(cb);
    callbackRef.current = cb;

    const callback = useCallback(
        HofUtil.debounced(() => {
            callbackRef.current(Globals.window().innerWidth);
        }, 10),
        []
    );

    useEventListener('resize', callback);
};
