import React from 'react';
import { StartupInfoPicker } from './StartupInfoPicker';
import { SpadeIsTrumpStarted } from './SpadeIsTrumpStarted';
import { ErrorBoundary } from '../../components/ErrorBoundary';
import { HistoryStore } from './history-store';
import { useSpadeIsTrumpSub } from './spadeIsTrump-store';
import { Footer, FooterButton } from '../../footer/Footer';
import { FooterUndo } from '../../footer/FooterUndo';
import { FooterRestart } from '../../footer/FooterRestart';
import { Icon } from '../../components/icons/icon';

type SpadeIsTrumpFooterProps = { started: boolean };

const SpadeIsTrumpFooter: React.FC<SpadeIsTrumpFooterProps> = ({ started }) => (
    <Footer>
        <FooterUndo onClick={HistoryStore.undo} />
        {started && <FooterRestart onClick={HistoryStore.reset} />}
        {started && (
            <FooterButton onClick={HistoryStore.rearrange} description="Umsortieren">
                <Icon.Refresh />
            </FooterButton>
        )}
    </Footer>
);

export const SpadeIsTrump: React.FC = () => {
    const started = useSpadeIsTrumpSub(({ gameState }) => !!gameState.length);

    return (
        <ErrorBoundary fix={HistoryStore.reset}>
            <>
                <div className="spade-is-trump">{started ? <SpadeIsTrumpStarted /> : <StartupInfoPicker />}</div>
                <SpadeIsTrumpFooter started={started} />
            </>
        </ErrorBoundary>
    );
};
