import { Persistore } from 'persistore';
import { Util } from '../../util/util';
import { AllData, Data, dataOptions, DataOption } from './data';
import { Color } from './types';
import { createStore } from 'react-use-sub';
import { calculateTurn, getLastTurn } from './again-logic';
import { allLetters } from './Letters';
import { ColorList } from './ColorPoints';

export enum SplashIconType {
    CANCEL = 'cancel',
    UNDO = 'undo',
    SUBMIT = 'submit',
    SKIP = 'skip',
}

export enum TurnType {
    ROW = 'row',
    COLOR = 'color',
    JOKER = 'joker',
    SKIP = 'skip',
    CHOSEN = 'chosen',
}

export type ChosenTurn = { color: Color; count: number; completedColor?: Color; completedRows?: boolean[] };

export type TurnState =
    | { type: TurnType.ROW; data: string }
    | { type: TurnType.COLOR; data: Color }
    | { type: TurnType.JOKER }
    | { type: TurnType.SKIP; index: number }
    | { type: TurnType.CHOSEN; data: ChosenTurn; index: number };

export type AgainState = {
    crossed: boolean[][];
    rowPointsBlocked: boolean[];
    colorPointsBlocked: boolean[];
    takenJokers: number;
    turns: TurnState[];
};

const uncrossedRow: boolean[] = Util.array(7).map(() => false);
export const uncrossedGrid: boolean[][] = Util.array(15).map(() => uncrossedRow);
const unblockedRowPoints: boolean[] = Util.array(15).map(() => false);
const unblockedColorPoints: boolean[] = Util.array(5).map(() => false);
const initialAgainState: AgainState = {
    crossed: uncrossedGrid,
    rowPointsBlocked: unblockedRowPoints,
    colorPointsBlocked: unblockedColorPoints,
    takenJokers: 0,
    turns: [],
};

export type Chosen = { color: Color; count: number };
export type AgainStartedState = {
    crossed: boolean[][];
    turnCrossed: boolean[][];
    chosen: Chosen | null;
    completedColor: Color | null;
};
const initialAgainStartedState: AgainStartedState = {
    crossed: uncrossedGrid,
    turnCrossed: uncrossedGrid,
    chosen: null,
    completedColor: null,
};

type State = {
    againSplash: SplashIconType | null;
    again: AgainState;
    againData: Data | null;
    againStarted: AgainStartedState;
};

const [useAgainSub, store] = createStore<State>({
    againSplash: null,
    again: initialAgainState,
    againData: null,
    againStarted: initialAgainStartedState,
});

export { useAgainSub };

const setSplash = (icon: SplashIconType | null) => store.set({ againSplash: icon });

const _key = 'again';
const _keyChosen = 'again_chosen';

const getHistory = (): AgainState[] => {
    try {
        const states = Persistore.get(_key);
        return JSON.parse(states as any);
    } catch (e) {
        return [];
    }
};

const randomNum = (limit: number) => Math.floor(Math.random() * limit);
const randomDataKey = (): DataOption => dataOptions[randomNum(dataOptions.length)];

const dataOption = (option: DataOption | 'random'): DataOption => (option === 'random' ? randomDataKey() : option);

const chooseData = (option: DataOption): void => {
    const selectedOption = dataOption(option);
    store.set({ againData: AllData[selectedOption] });
    Persistore.set(_keyChosen, selectedOption);
};

const setStarted = (updates: Partial<AgainStartedState>): void =>
    store.set(({ againStarted }) => ({ againStarted: { ...againStarted, ...updates } }));

const set = (state: Partial<AgainState>): void => {
    store.set(({ again }) => ({ again: { ...again, ...state } }));
    const updatedState = store.get().again;

    // update the crossed of the again started state
    setStarted({ crossed: updatedState.crossed });

    const history = getHistory();
    history.unshift(updatedState);
    if (history.length > 20) history.pop();
    Persistore.set(_key, JSON.stringify(history));
};

const undo = (): void => {
    if (!store.get().againSplash) {
        const history = getHistory();
        if (history.length > 1) {
            history.shift();
            Persistore.set(_key, JSON.stringify(history));
            store.set({ again: history[0], againSplash: SplashIconType.UNDO });
        } else {
            Persistore.remove(_key);
            store.set({ again: initialAgainState, againSplash: history.length > 0 ? SplashIconType.UNDO : null });
        }
    }
};

const get = (): AgainState => getHistory()[0] || initialAgainState;
const getChosenData = (): Data | null => {
    const option = Persistore.get(_keyChosen);
    return option ? AllData[(option as any) as DataOption] : null;
};

const init = () => {
    const againState = get();
    store.set({
        again: againState,
        againData: getChosenData(),
        againStarted: { ...initialAgainStartedState, crossed: againState.crossed },
    });
};

// TODO: Make this action revertable by moving the chosen state into the again state
const restart = (): void => Persistore.remove(_key) || Persistore.remove(_keyChosen) || init();

init();

const onCross = (boxIndex: number, rowIndex: number): void => {
    const {
        againStarted: { crossed, turnCrossed, chosen },
        againData,
    } = store.get();
    const currentRow = crossed[rowIndex];
    if (!currentRow[boxIndex] && againData) {
        const color = againData.grid[rowIndex][boxIndex];
        if (chosen) {
            if (color !== chosen.color) return; // many colors is not allowed
            const alreadyPicked = chosen.count;
            if (alreadyPicked > 4) return; // no more than 5 per color and turn
        }
        const updatedRow = Util.nextArray(currentRow, boxIndex, true);
        const updatedTurnRow = Util.nextArray(turnCrossed[rowIndex], boxIndex, true);
        const count = chosen ? chosen.count + 1 : 1;
        AgainStore.setStarted({
            crossed: Util.nextArray(crossed, rowIndex, updatedRow),
            turnCrossed: Util.nextArray(turnCrossed, rowIndex, updatedTurnRow),
            chosen: { color, count },
        });
    }
};

const onFinish = () => {
    const {
        againStarted: { crossed, chosen },
        againSplash,
        again,
        againData,
    } = store.get();
    if (!againSplash && againData) {
        const lastTurn = getLastTurn(again.turns);
        const lastTurnIndex = lastTurn ? (lastTurn as any).index : 0;
        const madeTurn = calculateTurn(chosen, again.crossed, crossed, againData, lastTurnIndex);

        AgainStore.set({ crossed, turns: [...again.turns, madeTurn] });
        AgainStore.setSplash(madeTurn.type === TurnType.SKIP ? SplashIconType.SKIP : SplashIconType.SUBMIT);
        AgainStore.setStarted({
            turnCrossed: uncrossedGrid,
            chosen: null,
            completedColor: madeTurn.type === TurnType.CHOSEN ? madeTurn.data.completedColor : undefined,
        });
    }
};

const onAbort = () => {
    const { againSplash, again } = store.get();
    if (!againSplash) {
        AgainStore.setSplash(SplashIconType.CANCEL);
        AgainStore.setStarted({
            turnCrossed: uncrossedGrid,
            chosen: null,
            crossed: again.crossed,
            completedColor: null,
        });
    }
};

const onClosePopup = () => AgainStore.setStarted({ completedColor: null });

const takeJoker = () => {
    const { takenJokers, turns } = get();
    AgainStore.set({
        takenJokers: takenJokers + 1,
        turns: [...turns, { type: TurnType.JOKER }],
    });
};

const blockRowPoints = (index: number) => {
    const { rowPointsBlocked, turns } = get();
    AgainStore.set({
        rowPointsBlocked: Util.nextArray(rowPointsBlocked, index, true),
        turns: [...turns, { type: TurnType.ROW, data: allLetters[index] }],
    });
};

const blockColorPoints = (index: number) => {
    const { colorPointsBlocked, turns } = get();
    AgainStore.set({
        colorPointsBlocked: Util.nextArray(colorPointsBlocked, index, true),
        turns: [...turns, { type: TurnType.COLOR, data: ColorList[index] }],
    });
};

export const AgainStore = {
    set,
    undo,
    restart,
    chooseData,
    setSplash,
    setStarted,
    onCross,
    onFinish,
    onAbort,
    onClosePopup,
    takeJoker,
    blockRowPoints,
    blockColorPoints,
};
