import React, { FocusEventHandler } from 'react';
import { ErrorMessage, iField } from 'morfi';
import { __, __string, Message, StringMessage } from 'first-intl';
import { DisplayError } from './DisplayError';
import { EventUtil } from '../../util/event-util';
import { Util } from '../../util/util';

export type AbstractRef<T> = { current: null | T };
export type InputRef = AbstractRef<HTMLInputElement>;

export type InputType = 'text' | 'number' | 'password' | 'email';

type InputHandler = (value: string) => void;

type CommonInputProps = {
    label?: Message;
    placeholder?: StringMessage;
    className?: string;
    inputClassName?: string;
    onFocus?: FocusEventHandler<HTMLInputElement>;
    autoFocus?: boolean;
    disabled?: boolean;
    type?: InputType;
    trim?: boolean;
    name?: string;
    forwardedRef?: InputRef;
    readOnly?: boolean;
};

type InputProps = {
    required?: boolean;
    onChange?: InputHandler;
    onBlur?: InputHandler;
    value: string;
    error?: ErrorMessage;
} & CommonInputProps;

const transformHandler = (handler: InputHandler, trim?: boolean): InputHandler => (value: string) =>
    handler(trim ? value && value.trim() : value);

export const Input: React.FC<InputProps> = ({
    value,
    label,
    placeholder,
    error,
    required,
    className,
    inputClassName,
    onChange,
    onBlur,
    onFocus,
    autoFocus,
    disabled,
    type = 'text',
    trim,
    name,
    forwardedRef,
    readOnly,
}) => (
    <div className={Util.classNames(className, required && 'required')}>
        {label && <label>{__(label)}</label>}
        <input
            name={name}
            className={Util.classNames(inputClassName, error && 'is-invalid')}
            onChange={onChange && EventUtil.inputHandler(transformHandler(onChange, trim))}
            onBlur={onBlur && EventUtil.inputHandler(transformHandler(onBlur, trim))}
            onFocus={onFocus}
            placeholder={placeholder && __string(placeholder)}
            ref={forwardedRef}
            readOnly={readOnly}
            {...{ value, autoFocus, disabled, type }}
        />
        {error && <DisplayError error={error} />}
    </div>
);

type FormInputProps = { Field: iField<string> } & CommonInputProps;

export const FormInput = ({ Field, className = 'form-control', ...rest }: FormInputProps) => (
    <Field>
        {({ onChange, onBlur, required, value, error }) => (
            <Input {...{ required, onBlur, onChange, className, value, error }} {...rest} />
        )}
    </Field>
);
