import { Util } from '../../util/util';
import { createStore } from 'react-use-sub';
import { Persistore } from 'persistore';
import { BONUS, Color } from './data';

const _key = 'veryClever';

export type Crossed = boolean[];
export type Points = number[];
export type Highlight = { color: Color; index: number };

const _setHistoryLocal = (history: GameState[]) => Persistore.set(_key, JSON.stringify(history));
const _getHistory = () => {
    try {
        const history = JSON.parse(Persistore.get(_key) || '[]');
        if (Array.isArray(history)) return history;
    } catch (e) {
        _setHistoryLocal([]);
    }
    return [];
};
const _get = () => _getHistory().pop();

export type GameState = {
    [Color.YELLOW]: Crossed;
    [Color.BLUE]: Crossed;
    [Color.GREEN]: Crossed;
    [Color.ORANGE]: Points;
    [Color.PURPLE]: Points;
    earnedBonus: {
        [Color.YELLOW]: BONUS[];
        [Color.BLUE]: BONUS[];
    };
    reshuffle: Crossed;
    plusOne: Crossed;
    round: number;
    selection: Color.ORANGE | Color.PURPLE | null;
    highlight: null | Highlight;
    bonus: BONUS[];
    foxes: number;
};
export const initialGameState = {
    [Color.YELLOW]: Util.arrayFill(Util.array(16, false), [3, 6, 9, 12], true),
    [Color.BLUE]: Util.arrayFill(Util.array(12, false), [0], true),
    [Color.GREEN]: Util.array(11, false),
    [Color.ORANGE]: Util.array(11, 0),
    [Color.PURPLE]: Util.array(11, 0),
    earnedBonus: {
        [Color.YELLOW]: [],
        [Color.BLUE]: [],
    },
    reshuffle: [false],
    plusOne: [],
    round: 1,
    selection: null,
    highlight: null,
    bonus: [],
    foxes: 0,
};
const _setLocal = (state: GameState) => _setHistoryLocal([..._getHistory().slice(-9), state]);

const [useVeryCleverSub, _Store] = createStore<GameState>(initialGameState);

const _set: typeof _Store['set'] = (update) => {
    _Store.set(update);
    _setLocal(_Store.get());
};

_Store.set(_get() || {});

const undo = () => {
    const history = _getHistory();
    if (history.length > 1) {
        history.pop();
        _setHistoryLocal(history);
        _Store.set(history[history.length - 1]);
    }
};

const reset = () => {
    _setHistoryLocal([]);
    _set(initialGameState);
};

export const VeryCleverStore = {
    get: _Store.get,
    set: _set,
    restart: () => _set(initialGameState),
    undo,
    reset,
};

export { useVeryCleverSub };
