import React from 'react';
import { AgainStore, useAgainSub } from './again-store';
import { Data } from './data';
import { Jokers } from './Jokers';
import { Grid } from './Grid';
import { Letters } from './Letters';
import { calculate, getClickable, getLastTurn } from './again-logic';
import { RowPoints } from './RowPoints';
import { ColorPoints } from './ColorPoints';
import { Total } from './Total';
import { Turns } from './Turn';
import { CompletionPopup } from './CompletionPopup';
import { ActionSplash } from './ActionSplash';
import { useAgainResize } from './again-resize';

export const AgainStarted: React.FC<{ gridData: Data }> = ({ gridData }) => {
    useAgainResize();
    const { again, againSplash, againStarted } = useAgainSub(({ again, againSplash, againStarted }) => ({
        again,
        againSplash,
        againStarted,
    }));
    const { crossed, chosen, turnCrossed, completedColor } = againStarted;

    const { rowPointsBlocked, colorPointsBlocked, takenJokers, turns } = again;
    const { completedRows, completedColors, totalPoints } = calculate(again, gridData);
    const { backgroundColor } = gridData;
    const clickable = chosen ? getClickable(turnCrossed, false) : getClickable(crossed);
    const lastTurn = getLastTurn(turns);

    return (
        <div className="again" style={{ backgroundColor }}>
            <ActionSplash />
            {completedColor && !againSplash && (
                <CompletionPopup color={completedColor} onClose={AgainStore.onClosePopup} />
            )}
            <div className="again-inner">
                <Grid
                    gridData={gridData}
                    crossed={crossed}
                    clickable={clickable}
                    onCross={AgainStore.onCross}
                    lastTurn={lastTurn}
                />
                <Letters />
                <RowPoints rowPointsBlocked={rowPointsBlocked} completedRows={completedRows} lastTurn={lastTurn} />
                <Jokers takenJokers={takenJokers} />
                <ColorPoints colorPointsBlocked={colorPointsBlocked} completedColors={completedColors} />
                <Total totalPoints={totalPoints} lastTurn={lastTurn} />
                <Turns turns={turns} />
            </div>
        </div>
    );
};
