const classNamesFilter = (arg: any) => !!arg && typeof arg === 'string';
const classNames = (...classes: any[]): string | undefined => classes.filter(classNamesFilter).join(' ') || undefined;

const array = <T>(num: number, defaultValue?: T): Array<T> => Array(num).fill(defaultValue);

const arrayFill = <T>(arr: T[], indices: number[], value: T): T[] => {
    const copy = [...arr];
    indices.forEach((i) => {
        copy[i] = value;
    });
    return copy;
};

const nextArray = <T>(before: T[], index: number, value: T): T[] => {
    const copy = [...before];
    copy.splice(index, 1, value);
    return copy;
};

const arrayShuffle = <T>(arr: T[]): T[] => {
    const copy = [...arr];
    const start = copy.length - 1;
    for (let i = start; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        [copy[i], copy[j]] = [copy[j], copy[i]];
    }
    return copy;
};

// optimized as much as possible
// call this function without arguments to generate a new uuid
type UuidParam = void;
const uuid = (a: UuidParam): string =>
    (a as any)
        ? ((a as any) ^ ((Math.random() * 16) >> ((a as any) / 4))).toString(16)
        : (([1e7] as any) + -1e3 + -4e3 + -8e3 + -1e11).replace(/[018]/g, uuid);

export const Util = { array, classNames, nextArray, uuid, arrayFill, arrayShuffle };
