import React, { useCallback } from 'react';
import { VeryCleverLogic } from './VeryClever-logic';
import { useVeryCleverSub } from './VeryClever-store';
import { Color } from './data';

export type ValueSelectionOptionProps = { value: number; color: Color };

export const ValueSelectionOption: React.FC<ValueSelectionOptionProps> = ({ value, color }) => {
    const onClick = useCallback(() => VeryCleverLogic.selectValue(value), [value]);
    return (
        <div className="value" style={{ backgroundColor: color }} onClick={onClick}>
            {value}
        </div>
    );
};

type ValueSelectionProps = { color: Color };

export const ValueSelection: React.FC<ValueSelectionProps> = ({ color }) => {
    const { prevVal } = useVeryCleverSub((state) => {
        const currentIdx = color === Color.PURPLE ? state[Color.PURPLE].findIndex((v) => !v) - 1 : -1;
        return { prevVal: currentIdx >= 0 ? state[Color.PURPLE][currentIdx] : 0 };
    });

    return (
        <div className="value-selection">
            <div className="value-selection-inner">
                {[1, 2, 3, 4, 5, 6].slice(prevVal === 6 ? 0 : prevVal).map((v) => (
                    <ValueSelectionOption key={v} color={color} value={v} />
                ))}
            </div>
            <div className="value-selection-overlay" onClick={VeryCleverLogic.clearSelection} />
        </div>
    );
};
