import React, { Fragment } from 'react';

const allReportedErrors: any[] = [];
const oldConsoleError = window.console.error;
window.console.error = (...args: any[]) => {
    allReportedErrors.push(...args);
    oldConsoleError(...args);
};
window.onerror = (e) => {
    allReportedErrors.push(e);
};

export type AdminProps = { close: () => void };

export const Admin: React.FC<AdminProps> = ({ close }) => (
    <div className="admin">
        <div className="console-error">
            <h1>console.error</h1>
            {allReportedErrors.map((message, index) => (
                <p key={index}>{String(message)}</p>
            ))}
        </div>
        <div className="localStorage">
            <h1>localStorage</h1>
            {Object.keys(localStorage).map((key) => (
                <Fragment key={key}>
                    <h3>{key}</h3>
                    <p>{JSON.stringify(localStorage.getItem(key))}</p>
                </Fragment>
            ))}
        </div>
        <button onClick={close}>Close</button>
    </div>
);
