import React, { useCallback, useEffect, useMemo, useRef, useState } from 'react';
import QrCode from 'qrcode-reader';
import { Code, CodeWords } from './Decrypto-store';
import { __ } from 'first-intl';

const openStream = (): Promise<MediaStream> =>
    navigator.mediaDevices.getUserMedia({ audio: false, video: { facingMode: 'environment' } });

type QrCodeScannerProps = { onInput: (input: { code: Code; codeWords: CodeWords }) => void; prepareClose: boolean };

export const QrCodeScanner: React.FC<QrCodeScannerProps> = ({ onInput, prepareClose }) => {
    const [permissionDenied, setPermissionDenied] = useState(false);
    const [error, setError] = useState<Error | undefined>();
    const [stream, setStream] = useState<MediaStream | undefined>();
    const streamRef = useRef<MediaStream | undefined>();
    streamRef.current = stream;
    const { width, height } = useMemo(
        () => ({
            width: window.innerWidth,
            height: window.innerHeight,
        }),
        []
    );
    const canvas = useRef<HTMLCanvasElement | null>(null);
    const video = useRef<HTMLVideoElement | null>(null);
    const isRunning = useRef(false);
    const createSnap = useRef(() => {});

    const clearStream = useCallback((): void => {
        streamRef.current!.getTracks().forEach((track) => streamRef.current!.removeTrack(track));
        video.current!.srcObject = null;
        video.current!.src = '';
    }, []);

    useEffect(() => {
        openStream()
            .then(setStream)
            .catch((e) => {
                if (e.name === 'NotAllowedError') {
                    // if encountered first time show the user the warning and consequently fail by trying to open
                    // the media stream, because that helps to notify him that we want to open the stream.
                    setPermissionDenied(true);
                } else {
                    // clear the interval, because will not be solved soon
                    setError(e);
                }
            });
    }, [clearStream]);

    useEffect(() => {
        if (prepareClose) {
            video.current!.pause(); // eslint-disable-line react-hooks/exhaustive-deps
            clearStream();
        }
    }, [prepareClose, clearStream]);

    const startScanning = useCallback((): void => {
        window.requestAnimationFrame(createSnap.current);
    }, []);

    useEffect(() => {
        const videoElem = video.current!;
        if (stream && !isRunning.current) {
            isRunning.current = true;
            if ('srcObject' in videoElem) {
                videoElem.srcObject = stream;
            } else {
                // Avoid using this in new browsers, as it is going away.
                videoElem!.src = window.URL.createObjectURL(stream);
            }
            // we have to set those attributes to empty strings, to ensure that
            // the video displays anything on safari ios
            videoElem.setAttribute('autoplay', '');
            videoElem.setAttribute('muted', '');
            videoElem.setAttribute('playsinline', '');
            startScanning();
        }
    }, [startScanning, stream]);

    const handleQrCodeResult = useCallback(
        (error: any, result: { result: string }) => {
            if (error || prepareClose) return startScanning();
            try {
                const { code, codeWords } = JSON.parse(atob(result.result));
                if (
                    code.every((p: any) => typeof p === 'number') &&
                    typeof codeWords.first === 'string' &&
                    typeof codeWords.second === 'string' &&
                    typeof codeWords.third === 'string'
                ) {
                    onInput({ code, codeWords });
                }
            } catch (e) {
                // do nothing
            }
            // in every case we need to start scanning again, because
            // the video stream needs to be active to get closed accordingly
            // else the browser does not know the stream can be removed
            startScanning();
        },
        [onInput, startScanning, prepareClose]
    );

    createSnap.current = useCallback((): void => {
        const context = canvas.current && canvas.current.getContext('2d');

        if (context) {
            const qr = new QrCode();
            (context as any).drawImage(video.current, 0, 0, width, height, 0, 0, width, height);
            const imageData = context.getImageData(0, 0, width, height);
            qr.callback = handleQrCodeResult;
            qr.decode(imageData);
        }
    }, [handleQrCodeResult, height, width]);

    if (permissionDenied) return <>{__('media.camAccess.required')}</>;
    if (error) return <>Ups, something went wrong (Code: QR2)</>;

    return (
        <div className="scanner">
            {stream && (
                <div>
                    <video ref={video} />
                    <canvas width={width} height={height} ref={canvas} />
                </div>
            )}
        </div>
    );
};
