import React from 'react';
import { AgainStore } from './again-store';
import { Box } from './Box';
import { Util } from '../../util/util';
import { Color } from './types';

const blockColorPoints = (index: number) => () => AgainStore.blockColorPoints(index);

export const ColorList: Color[] = 'OBGRY'.split('') as any;

type ColorPointsProps = { colorPointsBlocked: boolean[]; completedColors: boolean[] };
export const ColorPoints: React.FC<ColorPointsProps> = ({ colorPointsBlocked, completedColors }) => (
    <div className={Util.classNames('again-color-points')}>
        <div className="again-color-points-1">
            {ColorList.map((color: any, index) => (
                <Box
                    key={index}
                    content={5}
                    color={color}
                    crossed={colorPointsBlocked[index]}
                    onClick={blockColorPoints(index)}
                    circled={!colorPointsBlocked[index] && completedColors[index]}
                />
            ))}
        </div>
        <div className="again-color-points-2">
            {ColorList.map((color, index) => (
                <Box
                    key={index}
                    content={3}
                    color={color}
                    circled={colorPointsBlocked[index] && completedColors[index]}
                />
            ))}
        </div>
    </div>
);
