type Event = { preventDefault: Function; target: { value: any } };
type EventHandler = (event: Event) => void;

type CustomInputHandling = { preventDefault?: boolean };

const inputHandler = (
    cb: (_: any) => unknown | void,
    { preventDefault = true }: CustomInputHandling = {}
): EventHandler => (event) => {
    if (preventDefault) event.preventDefault();
    const original = event.target.value;
    cb(original);
};

const preventDefault = (event: any) => event.preventDefault();

export const EventUtil = { inputHandler, preventDefault };
