import React from 'react';
import { Code, CodeWords } from './Decrypto-store';
import { Util } from '../../util/util';

export type GuessResultProps = { codeWords: CodeWords; code: Code; guessed: Code };

export const GuessResult: React.FC<GuessResultProps> = ({ codeWords, code, guessed }) => {
    const firstIncorrect = guessed[0] !== code[0];
    const secondIncorrect = guessed[1] !== code[1];
    const thirdIncorrect = guessed[2] !== code[2];

    return (
        <div className="guessed">
            <div className="guess">
                <div className="code-word">{codeWords.first}</div>
                <span className={Util.classNames('code-guess', firstIncorrect ? 'invalid' : 'valid')}>
                    {guessed[0]}
                </span>
                {firstIncorrect && <span className="code-was">{code[0]}</span>}
            </div>
            <div className="guess">
                <div className="code-word">{codeWords.second}</div>
                <span className={Util.classNames('code-guess', secondIncorrect ? 'invalid' : 'valid')}>
                    {guessed[1]}
                </span>
                {secondIncorrect && <span className="code-was">{code[1]}</span>}
            </div>
            <div className="guess">
                <div className="code-word">{codeWords.third}</div>
                <span className={Util.classNames('code-guess', thirdIncorrect ? 'invalid' : 'valid')}>
                    {guessed[2]}
                </span>
                {thirdIncorrect && <span className="code-was">{code[2]}</span>}
            </div>
        </div>
    );
};
