import React from 'react';
import { useVeryCleverSub } from './VeryClever-store';
import { VeryCleverLogic } from './VeryClever-logic';
import { __ } from 'first-intl';

export const Points: React.FC = () => {
    const { points } = useVeryCleverSub((state) => ({ points: VeryCleverLogic.points(state) }));

    return (
        <div className="points-total">
            {__('VC.points')}
            {points}
        </div>
    );
};
