import React from 'react';
import { useVeryCleverSub } from './VeryClever-store';
import { AvailablePlusOneBonus, AvailableReshuffleBonus } from './AvailableBonus';
import { RoundOverview } from './RoundOverview';
import { ValueSelection } from './ValueSelection';
import { Color, Data } from './data';
import { BoxRow } from './BoxRow';
import { BoxGrid } from './BoxGrid';
import { Points } from './Points';
import { ActivatedBonus } from './ActivatedBonus';

export const VeryCleverInner: React.FC = () => {
    const { selection, bonus } = useVeryCleverSub(({ selection, bonus }) => ({ selection, bonus }));
    return (
        <div className="very-clever-outer">
            <div className="very-clever">
                <RoundOverview />
                <AvailableReshuffleBonus />
                <AvailablePlusOneBonus />
                <BoxGrid
                    color={Color.YELLOW}
                    contents={Data[Color.YELLOW].contents}
                    rowBonusList={Data[Color.YELLOW].rowBonus}
                    columnBonusList={Data[Color.YELLOW].columnBonus}
                />
                <BoxGrid
                    color={Color.BLUE}
                    contents={Data[Color.BLUE].contents}
                    rowBonusList={Data[Color.BLUE].rowBonus}
                    columnBonusList={Data[Color.BLUE].columnBonus}
                />
                <BoxRow
                    color={Color.GREEN}
                    contents={Data[Color.GREEN].contents}
                    gePrefix
                    bonusList={Data[Color.GREEN].bonus}
                    points={Data[Color.GREEN].points}
                />
                <BoxRow
                    color={Color.ORANGE}
                    contents={Data[Color.ORANGE].contents}
                    bonusList={Data[Color.ORANGE].bonus}
                />
                <BoxRow
                    color={Color.PURPLE}
                    contents={Data[Color.PURPLE].contents}
                    className="growing"
                    bonusList={Data[Color.PURPLE].bonus}
                />
                <Points />
                {selection && <ValueSelection color={selection} />}
                {bonus && <ActivatedBonus earned={bonus} />}
            </div>
        </div>
    );
};
