import React from 'react';
import { AgainStarted } from './AgainStarted';
import { DataSelection } from './DataSelection';
import { ErrorBoundary } from '../../components/ErrorBoundary';
import { AgainStore, useAgainSub } from './again-store';
import { Footer, FooterButton } from '../../footer/Footer';
import { FooterUndo } from '../../footer/FooterUndo';
import { FooterRestart } from '../../footer/FooterRestart';
import { Icon } from '../../components/icons/icon';
import { FooterTurnControl } from './Turn';

const FooterStarted = () => (
    <Footer className="top">
        <FooterUndo onClick={AgainStore.undo} />
        <FooterRestart onClick={AgainStore.restart} />
        <FooterButton onClick={AgainStore.onFinish} description="APP.skip">
            <Icon.Forward />
        </FooterButton>
    </Footer>
);

export const Again: React.FC = () => {
    const { againData, chosen } = useAgainSub(({ againData, againStarted: { chosen } }) => ({ againData, chosen }));
    if (!againData)
        return (
            <>
                <DataSelection />
                <Footer />
            </>
        );
    return (
        <ErrorBoundary fix={AgainStore.restart}>
            <>
                {chosen ? <FooterTurnControl chosen={chosen} /> : <FooterStarted />}
                <AgainStarted gridData={againData} />
            </>
        </ErrorBoundary>
    );
};
