import { useCallback, useEffect, useRef } from 'react';
import { Globals } from '../globals';

export const useEventListener = <K extends keyof WindowEventMap>(
    type: K,
    listener: (ev: WindowEventMap[K]) => any,
    // The event propagation through the DOM happens in two phases, the capture and the bubble phase.
    // During the capture phase the event propagates from the window down to the clicked element.
    // After that the event "bubbles" up again to the window. See https://javascript.info/bubbling-and-capturing for more details.
    // As a default the bubble phase is used to trigger the event listener.
    // If you want to use the capture phase set the following flag.
    capture?: boolean
) => {
    const ref = useRef<typeof listener>(listener);
    ref.current = listener;

    const wrapped = useCallback<typeof listener>((e) => {
        ref.current(e);
    }, []);

    useEffect(() => {
        Globals.window().addEventListener(type, wrapped, capture);
        return () => Globals.window().removeEventListener(type, wrapped, capture);
    }, [type, capture]); // eslint-disable-line react-hooks/exhaustive-deps
};
