import React from 'react';
import { DataOption, DataSelectionOptions } from './data';
import { AgainStore } from './again-store';
import { Select, SelectOption } from '../../components/form/select';

const placeHolderOption = { label: 'AGAIN.color.choose', value: undefined };
const randomOption = { label: 'AGAIN.color.random', value: 'random' as DataOption };
const onSelect = (option?: DataOption) => option && AgainStore.chooseData(option);

const OptionsWithRandom: SelectOption<DataOption | undefined>[] = [
    placeHolderOption,
    ...DataSelectionOptions,
    randomOption,
];

export const DataSelection: React.FC = () => (
    <div className="again-selection">
        <Select options={OptionsWithRandom} onChange={onSelect} value={undefined} />
    </div>
);
