import React from 'react';
import { Color, COLOR } from './types';
import { Util } from '../../util/util';

type BoxProps = {
    content?: string | number;
    color?: Color;
    customColor?: string;
    borderColor?: string;
    star?: boolean;
    circled?: boolean;
    crossed?: boolean;
    onClick?: () => void;
    flicker?: boolean;
};
export const Box: React.FC<BoxProps> = ({
    content,
    color,
    customColor,
    circled,
    crossed,
    onClick,
    star,
    borderColor,
    flicker,
}) => (
    <div>
        <div className={Util.classNames('box box-bordered', flicker && 'box-flicker')} onClick={onClick}>
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 50 50">
                <rect
                    x="0"
                    y="0"
                    width="50"
                    height="50"
                    rx="10"
                    ry="10"
                    fill={(color && COLOR[color]) || customColor || 'white'}
                />
                {borderColor && (
                    <rect
                        x="1"
                        y="1"
                        width="48"
                        height="48"
                        rx="10"
                        ry="10"
                        fill="none"
                        stroke={borderColor}
                        strokeWidth="3"
                    />
                )}
                {star && <polygon points="25 5 32 20 45 20 35 28 42 45 25 35 8 45 15 28 2 20 18 20" fill="white" />}
                {content !== undefined && (
                    <text textAnchor="middle" x="25" y="35" fontSize="30">
                        {content}
                    </text>
                )}
                {circled && <circle cx="25" cy="25" r="23" fill="none" stroke="black" strokeWidth="2" />}
                {crossed && (
                    <g stroke="black" strokeWidth="4">
                        <line x1="5" y1="5" x2="45" y2="45" />
                        <line x1="5" y1="45" x2="45" y2="5" />
                    </g>
                )}
            </svg>
        </div>
    </div>
);
