import React from 'react';
import { __string } from 'first-intl';
import { Util } from '../../util/util';
import { Icon } from '../../components/icons/icon';
import { Points, PointsState } from './Points';
import { HistoryStore } from './history-store';
import { useSpadeIsTrumpSub } from './spadeIsTrump-store';
import { GameState, PlayerState } from './players-store';
import { ClickableDescription } from './ClickableDescription';

const getActiveRound = (gameState: GameState, rounds: number): number | undefined => {
    const earnings = Util.array(rounds).map(() => 0);
    for (let i = 0; i < gameState.length; i++) {
        const points: PointsState[] = gameState[i].points;
        for (let j = 0; j < points.length; j++) {
            if (points[j].earned !== undefined) earnings[j]++;
        }
    }
    const round = earnings.findIndex((num) => num < gameState.length);
    return round === -1 ? undefined : round;
};

const activeRoundReady = (gameState: GameState, activeRound?: number): boolean => {
    if (activeRound === undefined) return true;
    for (let i = 0; i < gameState.length; i++) {
        const points = gameState[i].points[activeRound];
        if (points.tip === undefined) return false;
    }
    return true;
};

const roundHasError = (gameState: GameState, round: number, cards: number): boolean => {
    const earnings = gameState.map(
        (player) => player.points.map((points) => points.earned).filter((_, index) => index === round)[0]
    );
    if (earnings.filter((v) => v === undefined).length) return false; // only display warning if all earnings are known
    const positives = earnings.map((e: any) => (e > 10 ? e - 10 : undefined)).reduce((r: number, v = 0) => r + v, 0);
    return Boolean(positives > cards || (positives === cards && earnings.filter((v) => v === 0).length));
};

const onPlayerChange = (player: PlayerState, playerIdx: number) =>
    HistoryStore.set((gameState) => {
        const next = [...gameState];
        next[playerIdx] = player;
        return next;
    });

const onPointsChange = (player: PlayerState, playerIdx: number, roundIdx: number) => (points: PointsState) => {
    const next = [...player.points];
    next[roundIdx] = points;
    onPlayerChange({ ...player, points: next }, playerIdx);
};

export const SpadeIsTrumpStarted: React.FC = () => {
    const { gameState } = useSpadeIsTrumpSub(({ gameState }) => ({ gameState }));
    const rounds = gameState[0].points.length;
    const activeRound = getActiveRound(gameState, rounds);
    const earnedPoints = gameState.map((player) => player.points.reduce((res, val) => res + (val.earned || 0), 0));

    return (
        <table>
            <thead>
                <tr>
                    <ClickableDescription desc={__string('SIT.cardCount')} short="#" tag="th" className="card-number" />
                    {gameState.map(({ player: { name, shortcut } }, playerIdx) => (
                        <ClickableDescription desc={name} short={shortcut} key={playerIdx} tag="th" />
                    ))}
                </tr>
            </thead>
            <tbody>
                {Util.array(rounds).map((_, roundIdx: number) => {
                    const isActiveRound = activeRound === roundIdx;
                    const activeReady = activeRoundReady(gameState, activeRound);
                    const cards = rounds - roundIdx;
                    const hasError = roundHasError(gameState, roundIdx, cards);
                    return (
                        <tr
                            key={cards}
                            className={activeRound !== undefined && roundIdx > activeRound ? 'disabled' : undefined}>
                            <td>
                                {cards}
                                {hasError && (
                                    <span className="error-round">
                                        <Icon.Alert />
                                    </span>
                                )}
                            </td>
                            {gameState.map((player, playerIdx) => (
                                <Points
                                    key={playerIdx}
                                    points={player.points[roundIdx]}
                                    limit={cards}
                                    checkable={activeReady}
                                    onChange={onPointsChange(player, playerIdx, roundIdx)}
                                    active={roundIdx % gameState.length === playerIdx}
                                    activeRound={isActiveRound}
                                />
                            ))}
                        </tr>
                    );
                })}
                <tr className="sum">
                    <ClickableDescription desc={__string('SIT.points')} short="" tag="td" />
                    {earnedPoints.map((earned, earnedIdx) => (
                        <td key={earnedIdx}>
                            {earned}
                            {activeRound === undefined && earned === Math.max(...earnedPoints) && (
                                <Icon.Crown className="winner" />
                            )}
                        </td>
                    ))}
                </tr>
            </tbody>
        </table>
    );
};
