import { Color } from './types';
import { SelectOption } from '../../components/form/select';
import { StringMessage } from 'first-intl';

type RawData = { label: StringMessage; grid: string[]; stars: string; backgroundColor: string };
export type Data = { label: StringMessage; grid: Color[][]; stars: number[]; backgroundColor: string };

const parseRawGridData = (rowColors: string[]): Color[][] => rowColors.map((row: any) => row.split(''));
const parseRawStarPositions = (starPositions: string): number[] => starPositions.split('').map(Number);
const parseRawData = ({ label, grid, stars, backgroundColor }: RawData): Data => ({
    label,
    grid: parseRawGridData(grid),
    stars: parseRawStarPositions(stars),
    backgroundColor,
});

