export type DataOption = keyof typeof AllRawData;
export const AllData: { [key in DataOption]: Data } = {
    black: parseRawData(AllRawData.black),
    blue: parseRawData(AllRawData.blue),
    green: parseRawData(AllRawData.green),
    orange: parseRawData(AllRawData.orange),
    pink: parseRawData(AllRawData.pink),
    violet: parseRawData(AllRawData.violet),
    yellow: parseRawData(AllRawData.yellow),
};
export const dataOptions: DataOption[] = Object.keys(AllData) as any;

export const DataSelectionOptions: Array<SelectOption<DataOption>> = dataOptions.map(
    (key: DataOption): SelectOption<DataOption> => ({
        label: AllData[key].label,
        value: key,
    })
);
