import { Data } from './data';
import { AgainState, Chosen, TurnState, TurnType } from './again-store';
import { ColorList } from './ColorPoints';

const rowPoints1 = '533322212223335'.split('').map(Number);
const rowPoints2 = '322211101112223'.split('').map(Number);

export const getCompletedRows = (crossed: boolean[][]): boolean[] =>
    crossed.map((row) => row.every((checked) => checked));
export const getCompletedColors = (crossed: boolean[][], gridData: Data): boolean[] =>
    'OBGRY'
        .split('')
        .map((color) =>
            crossed.every((row, index) =>
                row.every((checked, boxIndex) => gridData.grid[index][boxIndex] !== color || checked)
            )
        );

const getTotalPoints = (
    again: AgainState,
    gridData: Data,
    completedRows: boolean[],
    completedColors: boolean[]
): number => {
    const { rowPointsBlocked, colorPointsBlocked, crossed, takenJokers } = again;
    const rowPoints = completedRows
        .map((completed, index) => (!completed ? 0 : rowPointsBlocked[index] ? rowPoints2[index] : rowPoints1[index]))
        .reduce((r, v) => r + v, 0);
    const colorPoints = completedColors
        .map((completed, index) => (!completed ? 0 : colorPointsBlocked[index] ? 3 : 5))
        .reduce((r, v) => r + v, 0 as number);
    const jokerPoints = 8 - takenJokers;
    const starPoints = crossed
        .map((row, index) => (row[gridData.stars[index]] ? 0 : -2))
        .reduce((r, v) => r + v, 0 as number);

    return rowPoints + colorPoints + jokerPoints + starPoints;
};

export const getClickable = (crossed: boolean[][], allowMiddle: boolean = true): boolean[][] =>
    crossed.map((row, rowIndex) =>
        row.map(
            (checked, boxIndex) =>
                !checked &&
                ((allowMiddle && rowIndex === 7) || // is allowed middle row
                (crossed[rowIndex - 1] || {})[boxIndex] || // left box is checked
                (crossed[rowIndex + 1] || {})[boxIndex] || // right box is checked
                row[boxIndex - 1] || // upper box is checked
                    row[boxIndex + 1]) // below box is checked
        )
    );

type Calculation = { completedRows: boolean[]; completedColors: boolean[]; totalPoints: number };

export const calculate = (again: AgainState, gridData: Data): Calculation => {
    const completedRows = getCompletedRows(again.crossed);
    const completedColors = getCompletedColors(again.crossed, gridData);
    const totalPoints = getTotalPoints(again, gridData, completedRows, completedColors);
    return { completedRows, completedColors, totalPoints };
};

export const calculateTurn = (
    chosen: Chosen | null,
    lastCrossed: boolean[][],
    crossed: boolean[][],
    gridData: Data,
    lastIndex: number
): TurnState => {
    if (chosen) {
        const completedRows = getCompletedRows(lastCrossed);
        const nextCompletedRows = getCompletedRows(crossed);
        const newCompletedRows = nextCompletedRows.map((row, index) => row && !completedRows[index]);

        const nextCompletedColors = getCompletedColors(crossed, gridData);
        const completedColors = getCompletedColors(lastCrossed, gridData);

        const newCompletedColor = nextCompletedColors
            .map((row, index) => row && !completedColors[index])
            .findIndex(Boolean);
        return {
            type: TurnType.CHOSEN,
            data: {
                ...chosen,
                completedRows: newCompletedRows,
                completedColor: ColorList[newCompletedColor] as any,
            },
            index: lastIndex + 1,
        };
    }
    return { type: TurnType.SKIP, index: lastIndex + 1 };
};

export const getLastTurn = (turns: TurnState[]): TurnState | undefined => {
    const turnsWithIndex = turns.filter((turn: any) => typeof turn.index === 'number');
    return turnsWithIndex.length > 0 ? turnsWithIndex[turnsWithIndex.length - 1] : undefined;
};
