import React from 'react';
import { AgainStore, TurnState, TurnType } from './again-store';
import { Box } from './Box';
import { Util } from '../../util/util';
import { RESIZE_CLASS } from './again-resize';

const rowPoints1 = '533322212223335'.split('').map(Number);
const rowPoints2 = '322211101112223'.split('').map(Number);

const blockRowPoints = (index: number) => () => AgainStore.blockRowPoints(index);

export const rowWasCompleted = (index: number, lastTurn?: TurnState): boolean =>
    Boolean(
        lastTurn &&
            lastTurn.type === TurnType.CHOSEN &&
            lastTurn.data.completedRows &&
            lastTurn.data.completedRows[index]
    );

const COMPLETED = '#5eee61';

type RowPointsProps = { rowPointsBlocked: boolean[]; completedRows: boolean[]; lastTurn?: TurnState };
export const RowPoints: React.FC<RowPointsProps> = ({ rowPointsBlocked, completedRows, lastTurn }) => (
    <div className={Util.classNames('again-row-points', RESIZE_CLASS)}>
        <div className="again-row-points-1">
            {rowPoints1.map((num, index) => (
                <Box
                    key={index}
                    content={num}
                    crossed={rowPointsBlocked[index]}
                    onClick={rowPointsBlocked[index] ? undefined : blockRowPoints(index)}
                    circled={!rowPointsBlocked[index] && completedRows[index]}
                    customColor={!rowPointsBlocked[index] && rowWasCompleted(index, lastTurn) ? COMPLETED : undefined}
                />
            ))}
        </div>
        <div className="again-row-points-2">
            {rowPoints2.map((num, index) => (
                <Box
                    key={index}
                    content={num}
                    circled={rowPointsBlocked[index] && completedRows[index]}
                    customColor={rowPointsBlocked[index] && rowWasCompleted(index, lastTurn) ? COMPLETED : undefined}
                />
            ))}
        </div>
    </div>
);
