import React from 'react';
import { createStore } from 'react-use-sub';
import { QrCode, QrCodeColorOptions } from './QrCode';
import { QrCodeScanner } from './QrCodeScanner';
import { Code, CodeWords, DecryptoStore } from './Decrypto-store';
import { Icon } from '../../components/icons/icon';

type QrCodeState = { info: string | null; scanning: boolean; closingScanner: boolean };
const initialQrCodeState = { info: null, scanning: false, closingScanner: false };

const [useQrCodeInfo, qrCodeInfoStore] = createStore<QrCodeState>(initialQrCodeState);

export const closeQrCodeOverlay = () => {
    const { closingScanner, scanning } = qrCodeInfoStore.get();
    if (closingScanner) return;
    else if (scanning) {
        qrCodeInfoStore.set({ closingScanner: true });
        // we need to close the scanner in two different phases
        // 1. notify the scanner to clear the active video stream
        // 2. remove the HTML elements by closing the React component
        // this has to be done with a certain delay, which is quite high, but
        // the user will be displayed immediate feedback by hiding the HTML elements
        setTimeout(() => qrCodeInfoStore.set(initialQrCodeState), 1000);
    } else {
        qrCodeInfoStore.set(initialQrCodeState);
    }
};
export const openQrCodeInfoOverlay = (info: string) => qrCodeInfoStore.set({ info });
export const openQrCodeScannerOverlay = () => qrCodeInfoStore.set({ scanning: true });

const _onScanInput = ({ code, codeWords }: { code: Code; codeWords: CodeWords }) => {
    closeQrCodeOverlay();
    DecryptoStore.set(({ enemy }) => ({ enemy: { ...enemy, code, codeWords, round: enemy.round + 1 } }));
};

export const QrCodeOverlay: React.FC = () => {
    const { info, scanning, closingScanner } = useQrCodeInfo((state) => state);
    const width = Math.min(window.innerWidth, window.innerHeight);

    return info ? (
        <div className="qr-code">
            <QrCode info={info} options={{ color: QrCodeColorOptions.black, width }} />
            <button onClick={closeQrCodeOverlay}>
                <Icon.Times />
            </button>
        </div>
    ) : scanning ? (
        <div className="qr-scanning" style={closingScanner ? { display: 'none' } : undefined}>
            <QrCodeScanner onInput={_onScanInput} prepareClose={closingScanner} />
            <button onClick={closeQrCodeOverlay}>
                <Icon.Times />
            </button>
        </div>
    ) : null;
};
