import React, { ReactNode, ReactNodeArray, useCallback, useEffect, useState } from 'react';
import { Icon } from '../components/icons/icon';
import { __, Message } from 'first-intl';
import { ClientHistory } from '../globals';
import { Util } from '../util/util';

export type FooterButtonProps = {
    onClick?: () => void;
    description?: Message;
    children: ReactNode;
    className?: string;
};

export const FooterButton: React.FC<FooterButtonProps> = ({ onClick, description, children, className }) => (
    <div className={Util.classNames('footer-action', className, !onClick && 'no-handler')} onClick={onClick}>
        {children}
        {description && <div className="small">{__(description)}</div>}
    </div>
);

const FooterHome: React.FC = () => (
    <FooterButton onClick={ClientHistory.back} description="APP.home">
        <Icon.Home />
    </FooterButton>
);

const applyFooterHeight = (open: boolean) =>
    document.getElementById('root')!.style.setProperty('--footer-height', open ? '3rem' : '0px');

type FooterProps = {
    expandable?: boolean;
    hideHome?: boolean;
    className?: string;
    children?: ReactNodeArray | ReactNode;
};

export const Footer: React.FC<FooterProps> = ({ children, expandable, hideHome, className }) => {
    const [open, setOpen] = useState(true);
    const toggle = useCallback(() => setOpen((o) => !o), []);

    useEffect(() => {
        if (!expandable) setOpen(true);
    }, [expandable]);

    useEffect(() => {
        applyFooterHeight(open);
    }, [open]);

    useEffect(() => () => applyFooterHeight(false), []);

    if (!open)
        return (
            <footer className={className}>
                <div className="footer-handle closed" onClick={toggle}>
                    <Icon.ChevronUp />
                </div>
            </footer>
        );

    return (
        <footer className={className}>
            {expandable && (
                <div className="footer-handle" onClick={toggle}>
                    <Icon.ChevronDown />
                </div>
            )}
            <div className="footer-border" />
            <nav>
                <div className="footer-actions">
                    {hideHome || <FooterHome />}
                    {children}
                </div>
            </nav>
        </footer>
    );
};
