import React, { useCallback, useMemo, useState } from 'react';
import { Code, CodePoint, CodeWords } from './Decrypto-store';
import { SelectOverlay, SelectOverlayOption } from '../../components/form/SelectOverlay';
import { Icon } from '../../components/icons/icon';
import { __ } from 'first-intl';
import { Util } from '../../util/util';

export type GuessCodeProps = { onSubmit: (guessed: Code) => void; codeWords: CodeWords };
const GuessOptions: SelectOverlayOption<CodePoint>[] = [
    { value: 1 as CodePoint, node: 1 },
    { value: 2 as CodePoint, node: 2 },
    { value: 3 as CodePoint, node: 3 },
    { value: 4 as CodePoint, node: 4 },
];

const _getDuplicated = (first?: CodePoint, second?: CodePoint, third?: CodePoint): number | undefined => {
    if (first && first === second) return first;
    if (third && (third === second || third === first)) return third;
};
const _getAllDuplicated = (first?: CodePoint, second?: CodePoint, third?: CodePoint): [boolean, boolean, boolean] => {
    const duplicate = _getDuplicated(first, second, third);
    return duplicate ? [duplicate === first, duplicate === second, duplicate === third] : [false, false, false];
};

export const GuessCode: React.FC<GuessCodeProps> = ({ onSubmit, codeWords }) => {
    const [first, setFirst] = useState<CodePoint | undefined>();
    const [second, setSecond] = useState<CodePoint | undefined>();
    const [third, setThird] = useState<CodePoint | undefined>();
    const duplicated = _getAllDuplicated(first, second, third);

    const wasGuessed = useMemo<boolean>(
        () => !!(first && second && third && first !== second && first !== third && second !== third),
        [first, second, third]
    );

    const onGuessedMe = useCallback(() => {
        onSubmit([first!, second!, third!]);
    }, [onSubmit, first, second, third]);

    return (
        <div className="guessing">
            <div className="guess">
                <div className="code-word">{codeWords.first}</div>
                <SelectOverlay options={GuessOptions} onChange={setFirst}>
                    <span className={Util.classNames('code-guess', duplicated[0] && 'invalid')}>{first ?? '?'}</span>
                </SelectOverlay>
            </div>
            <div className="guess">
                <div className="code-word">{codeWords.second}</div>
                <SelectOverlay options={GuessOptions} onChange={setSecond}>
                    <span className={Util.classNames('code-guess', duplicated[1] && 'invalid')}>{second ?? '?'}</span>
                </SelectOverlay>
            </div>
            <div className="guess">
                <div className="code-word">{codeWords.third}</div>
                <SelectOverlay options={GuessOptions} onChange={setThird}>
                    <span className={Util.classNames('code-guess', duplicated[2] && 'invalid')}>{third ?? '?'}</span>
                </SelectOverlay>
            </div>
            {wasGuessed && (
                <button onClick={onGuessedMe} type="button">
                    <Icon.Key />
                    <div className="description">{__('DECRYPTO.btn.decode')}</div>
                </button>
            )}
        </div>
    );
};
