import React, { Fragment } from 'react';
import { Bonus } from './Bonus';
import { Util } from '../../util/util';
import { Box } from './Box';
import { Icon } from '../../components/icons/icon';
import { BONUS, Color } from './data';

type BoxGridProps = {
    color: Exclude<Color, Color.BLACK>;
    contents: Array<string | number>;
    rowBonusList: BONUS[];
    columnBonusList: BONUS[];
};

const array6 = Util.array(6);

export const BoxGrid = ({ color, contents, rowBonusList, columnBonusList }: BoxGridProps) => (
    <div className="grid-box-outer">
        <div className="grid-box" style={{ borderColor: color, color }}>
            {Util.array(contents.length / 4).map((_, row) => (
                <Fragment key={row}>
                    {contents.slice(row * 4, row * 4 + 4).map((content, key) => (
                        <div className="grid-item" key={key}>
                            <Box key={key} content={content} color={color} index={4 * row + key} />
                        </div>
                    ))}
                    <div className="grid-item grid-arrow">
                        <Icon.ArrowRight />
                    </div>
                    <div className="grid-item">
                        <Bonus bonus={rowBonusList[row]} />
                    </div>
                </Fragment>
            ))}
            {array6.map((_, i) => (
                <div className="grid-item grid-arrow" key={i}>
                    {i < columnBonusList.length ? (
                        i === 4 ? (
                            <Icon.ArrowRight className="rotate-45" />
                        ) : (
                            <Icon.ArrowDown />
                        )
                    ) : null}
                </div>
            ))}
            {}
            {columnBonusList.map((bonus, i) => (
                <Fragment key={i}>
                    {i === 4 && <div className="grid-item" />}
                    <div className="grid-item">
                        <Bonus bonus={bonus} />
                    </div>
                </Fragment>
            ))}
        </div>
    </div>
);
