import React from 'react';
import { Color, COLOR } from './types';

const VALUE_SVG = [
    <text x="110" y="420" fontSize="500" key="0">
        ?
    </text>,
    <circle cx="250" cy="250" r="50" fill="black" key="1" />,
    <g fill="black" key="2">
        <circle cx="125" cy="375" r="50" />
        <circle cx="375" cy="125" r="50" />
    </g>,
    <g fill="black" key="3">
        <circle cx="125" cy="375" r="50" />
        <circle cx="250" cy="250" r="50" />
        <circle cx="375" cy="125" r="50" />
    </g>,
    <g fill="black" key="4">
        <circle cx="125" cy="375" r="50" />
        <circle cx="125" cy="125" r="50" />
        <circle cx="375" cy="375" r="50" />
        <circle cx="375" cy="125" r="50" />
    </g>,
    <g fill="black" key="5">
        <circle cx="125" cy="375" r="50" />
        <circle cx="125" cy="125" r="50" />
        <circle cx="250" cy="250" r="50" />
        <circle cx="375" cy="375" r="50" />
        <circle cx="375" cy="125" r="50" />
    </g>,
];

type CubeProps = { count?: number; color?: Color };

export const Cube: React.FC<CubeProps> = ({ count, color }) => {
    const innerValue = count !== undefined ? VALUE_SVG[count] : undefined;

    return (
        <div className="cube">
            <svg className="icon" viewBox="0 0 500 500">
                <rect x="0" y="0" width="500" height="500" rx="100" ry="100" fill="white" />
                {innerValue || (
                    <g stroke={(color && COLOR[color]) || 'black'} strokeWidth="100">
                        <line x1="80" y1="80" x2="420" y2="420" />
                        <line x1="80" y1="420" x2="420" y2="80" />
                    </g>
                )}
            </svg>
        </div>
    );
};
