import React, { Component, ReactNode } from 'react';
import { __ } from 'first-intl';

type ErrorBoundaryProps = { fix: () => void; children: ReactNode };
type ErrorBoundaryState = { hasError: boolean };

export class ErrorBoundary extends Component<ErrorBoundaryProps, ErrorBoundaryState> {
    state: ErrorBoundaryState = { hasError: false };

    static getDerivedStateFromError = (): ErrorBoundaryState => ({ hasError: true });

    render() {
        const { hasError } = this.state;
        const { children, fix } = this.props;

        if (hasError) {
            return (
                <div className="game-error">
                    <h2>{__('error.boundary.unexpected')}</h2>
                    <p>{__('error.boundary.sorry')}</p>
                    <button type="button" onClick={fix}>
                        {__('error.boundary.fix')}
                    </button>
                </div>
            );
        }
        return children;
    }
}
