import { Globals } from '../globals';

/**
 * The wrapped function will be called debounced with a certain specified delay.
 */
const debounced = <T extends (...arg: any[]) => void>(func: T, delay: number): T => {
    let lastPending = 0;
    return ((...args: any[]) => {
        Globals.window().clearTimeout(lastPending);
        lastPending = Globals.window().setTimeout(() => func(...args), delay);
    }) as any;
};

/*
 * Hof is a shortcut for (Higher Order Function).
 */
export const HofUtil = { debounced };
