import React from 'react';
import { Turn } from './Turn';
import { TurnState } from './again-store';
import { __ } from 'first-intl';

type TotalProps = { totalPoints: number; lastTurn?: TurnState };
export const Total: React.FC<TotalProps> = ({ totalPoints, lastTurn }) => (
    <div className="again-total">
        {__('AGAIN.points')}
        <div className="again-total-points">{totalPoints}</div>
        {lastTurn && (
            <div className="again-last-turn">
                {__('AGAIN.lastTurn')}
                <Turn hideIndex turn={lastTurn} />
            </div>
        )}
    </div>
);
