import React, { useCallback, useEffect, useRef, useState } from 'react';
import { useDecryptoSub } from './Decrypto-store';
import { __ } from 'first-intl';

enum Status {
    WON = 'WON',
    LOST = 'LOST',
}

export const NotifyResult: React.FC = () => {
    const [status, setStatus] = useState<Status | undefined>();
    const { wins, losses } = useDecryptoSub(({ results }) => results);
    const last = useRef({ wins, losses });

    const close = useCallback(() => setStatus(undefined), []);

    useEffect(() => {
        if (last.current.wins === 1 && wins === 2) {
            setStatus(Status.WON);
        } else if (last.current.losses === 1 && losses === 2) {
            setStatus(Status.LOST);
        }
        last.current = { wins, losses };
    }, [wins, losses]);

    if (!status) return null;

    return (
        <div className="result-overlay" onClick={close}>
            <span>{__(status === Status.WON ? 'DECRYPTO.won' : 'DECRYPTO.lost')}</span>
        </div>
    );
};
