import React, { ReactNode, useCallback, useState } from 'react';

export type SelectOverlayValueProps<T> = {
    value: T;
    children: ReactNode;
    onChange: (value: T) => void;
    close: () => void;
};

export const SelectOverlayValue = <T extends any>({ value, children, onChange, close }: SelectOverlayValueProps<T>) => {
    const onClick = useCallback(() => {
        onChange(value);
        close();
    }, [onChange, close, value]);
    return (
        <div className="value" onClick={onClick}>
            {children}
        </div>
    );
};

export type SelectOverlayOption<T> = { value: T; node: ReactNode };
export type SelectOverlayProps<T> = {
    children: ReactNode;
    options: SelectOverlayOption<T>[];
    onChange: (value: T) => void;
};

export const SelectOverlay = <T extends any>({ children, options, onChange }: SelectOverlayProps<T>) => {
    const [isOpen, setOpen] = useState<boolean>(false);

    const close = useCallback(() => setOpen(false), []);
    const open = useCallback(() => setOpen(true), []);

    return (
        <>
            <span onClick={open}>{children}</span>
            {isOpen && (
                <div className="select-overlay">
                    <div className="select-overlay-inner">
                        {options.map(({ value, node }, index) => (
                            <SelectOverlayValue key={index} close={close} onChange={onChange} value={value}>
                                {node}
                            </SelectOverlayValue>
                        ))}
                    </div>
                    <div className="select-overlay-outer" onClick={close} />
                </div>
            )}
        </>
    );
};
