import React from 'react';
import { Util } from '../../util/util';
import { Code, CodeWords, DecryptoStore, useDecryptoSub } from './Decrypto-store';
import { CodeWordsSelection } from './CodeWordsSelection';
import { GuessCode } from './GuessCode';
import { GuessResult } from './GuessResult';
import { KnowledgeView } from './KnowledgeView';
import { ShowQrCode } from './ShowQrCode';
import { __ } from 'first-intl';

const _randomCode = (): Code => Util.arrayShuffle([1, 2, 3, 4]).slice(0, 3) as Code;
const _generateCode = () =>
    DecryptoStore.set(({ me }) => ({
        me: { ...me, code: _randomCode(), codeWords: undefined, guessed: undefined, round: me.round + 1 },
    }));
const _setCodeWords = (codeWords: CodeWords) => DecryptoStore.set(({ me }) => ({ me: { ...me, codeWords } }));
const _setGuessed = (guessed: Code) =>
    DecryptoStore.set(({ me, results: { wins, losses } }) => {
        const { codeWords, code } = me;
        const { first, second, third } = codeWords!;
        const wrongGuess = guessed[0] !== code![0] || guessed[1] !== code![1] || guessed[2] !== code![2];
        const knowledge: any = [...me.knowledge].map((arr) => [...arr]);
        knowledge[code![0] - 1].push(first);
        knowledge[code![1] - 1].push(second);
        knowledge[code![2] - 1].push(third);
        const updatedMe = { me: { ...me, knowledge, guessed } };
        return wrongGuess ? { ...updatedMe, results: { wins, losses: losses + 1 } } : updatedMe;
    });

export const DecryptoMe: React.FC = () => {
    const { code, codeWords, guessed, knowledge, round } = useDecryptoSub(({ me }) => me);
    const disableGeneration = useDecryptoSub(({ enemy: { guessed, round }, me, results: { wins, losses } }) => {
        const finishedEnemyRound = guessed ? round : round - 1;
        const meRound = me.round;
        const lostOrWon = losses > 1 || wins > 1;
        return lostOrWon || (meRound > 0 && finishedEnemyRound !== meRound);
    });

    const noCodeOrFinishedGuessing = !code || !!(code && codeWords && guessed);

    return (
        <>
            {round > 0 && (
                <div className="round">
                    {__('DECRYPTO.round')} {round}
                </div>
            )}
            {noCodeOrFinishedGuessing && (
                <div className="generate-code">
                    <button onClick={_generateCode} type="button" disabled={disableGeneration}>
                        ?.?.?
                        <div className="description">{__('DECRYPTO.btn.generate')}</div>
                    </button>
                </div>
            )}
            {code && !codeWords && <CodeWordsSelection onSubmit={_setCodeWords} code={code} />}
            {code && codeWords && !guessed && <GuessCode onSubmit={_setGuessed} codeWords={codeWords} />}
            {code && codeWords && guessed && <GuessResult codeWords={codeWords} code={code} guessed={guessed} />}
            {code && codeWords && <ShowQrCode code={code} codeWords={codeWords} />}
            <KnowledgeView knowledge={knowledge} />
        </>
    );
};
