import React, { useCallback } from 'react';
import { Color } from './types';
import { Box } from './Box';
import { Data } from './data';
import { Util } from '../../util/util';
import { TurnState } from './again-store';
import { RESIZE_CLASS } from './again-resize';
import { rowWasCompleted } from './RowPoints';

type GridProps = {
    gridData: Data;
    crossed: boolean[][];
    clickable: boolean[][];
    onCross: (boxIndex: number, rowIndex: number) => void;
    lastTurn?: TurnState;
};

export const Grid: React.FC<GridProps> = ({ gridData, crossed, clickable, lastTurn, onCross }) => {
    const onClickHandler = useCallback((boxIndex: number, rowIndex: number) => () => onCross(boxIndex, rowIndex), [
        onCross,
    ]);
    return (
        <div className={Util.classNames('again-grid', RESIZE_CLASS)}>
            {gridData.grid.map((row: Color[], rowIndex: number) => (
                <div
                    className={Util.classNames('again-row', rowWasCompleted(rowIndex, lastTurn) && 'row-alert')}
                    key={rowIndex}>
                    {row.map((color, boxIndex) => {
                        const starPosition = gridData.stars[rowIndex];
                        const crossedRow = crossed[rowIndex];
                        const isMiddleRow = rowIndex === 7;
                        const isClickable = clickable[rowIndex][boxIndex];

                        return (
                            <Box
                                onClick={isClickable ? onClickHandler(boxIndex, rowIndex) : undefined}
                                key={boxIndex}
                                color={color}
                                star={starPosition === boxIndex}
                                crossed={crossedRow[boxIndex]}
                                borderColor={isMiddleRow ? 'white' : 'black'}
                            />
                        );
                    })}
                </div>
            ))}
        </div>
    );
};
