/**
 * Wording conventions:
 * 1) search string => Part of the url preceded by the pathname.
 *      e.g.: ?uuid=whatever&page=4
 * 2) query string => search string without "?"
 *      e.g.: uuid=whatever&page=4
 * 3) query => the associated object of the query string
 *      e.g.: {uuid: 'whatever', page: '4'}
 *
 * ATTENTION: All values will be strings!!!
 */

/**
 * Transforms location search string into query object.
 */
const locationSearchToQuery = (search: string = ''): { [key: string]: any } => {
    const s = search.startsWith('?') ? search.substr(1) : search; // hash history not considered for now
    const params = s.split('&');
    const result = {} as { [key: string]: any };
    for (let i = 0; i < params.length; i++) {
        const param = params[i];
        if (param) {
            const pair = param.split('=');
            const value = pair[1];
            if (value !== undefined) result[decodeURIComponent(pair[0])] = decodeURIComponent(value);
        }
    }
    return result;
};

/**
 * Transforms query object to query string.
 */
const queryToString = (query: { [key: string]: any }): string =>
    Object.keys(query)
        .map((key) => {
            const value = query[key];
            if (value !== undefined && value !== null) {
                return `${encodeURIComponent(key)}=${encodeURIComponent(String(value))}`;
            }
            return undefined;
        })
        .filter(Boolean)
        .join('&');

/**
 * Transforms query object to search string.
 */
const queryToSearch = (query: Object): string => {
    const queryString = queryToString(query);
    return queryString ? `?${queryString}` : '';
};

export const UrlUtil = { queryToString, locationSearchToQuery, queryToSearch };
