import React from 'react';
import { useVeryCleverSub } from './VeryClever-store';
import { VeryCleverLogic } from './VeryClever-logic';
import { __ } from 'first-intl';

export const RoundOverview: React.FC = () => {
    const { round } = useVeryCleverSub(({ round }) => ({ round }));
    return (
        <div className="round-overview">
            <span className="title">
                {__('VC.round')}
                {round}
            </span>
            <button onClick={VeryCleverLogic.nextRound}>{__('VC.next')}</button>
        </div>
    );
};
