import React from 'react';
import { Color } from './types';
import { Cube } from './Cube';
import { __ } from 'first-intl';

type CompletionPopupProps = { color: Color; onClose: () => void };

export const CompletionPopup = ({ color, onClose }: CompletionPopupProps) => (
    <div className="again-popup" onClick={onClose}>
        <div className="popup-overlay" />
        <div className="popup-content">
            {__('AGAIN.color.finished')}
            <Cube color={color} />
        </div>
    </div>
);
