import React, { ReactElement } from 'react';
import { __, __string, Message, StringMessage } from 'first-intl';
import { DisplayError } from './DisplayError';
import { ErrorMessage, iField } from 'morfi';
import { Util } from '../../util/util';
import { EventUtil } from '../../util/event-util';

export type SelectOption<T> = { label: StringMessage | string; value: T };

type SelectHandler<T> = (value: T) => void;

type CommonSelectProps<T> = {
    name?: string;
    label?: Message;
    className?: string;
    options: ReadonlyArray<SelectOption<T>>;
    disabled?: boolean;
    serialize?: (value: T) => string;
};

type SelectProps<T> = {
    required?: boolean;
    onChange?: SelectHandler<T>;
    onBlur?: SelectHandler<T>;
    value: T;
    error?: ErrorMessage;
} & CommonSelectProps<T>;

/**
 * ATTENTION: Since react v16.6 you have to supply strings or numbers within <option> tags.
 *            So you should not use any placeholders inside the messages you use as
 *            label for the select options.
 */
const Option: React.FC<{ label: StringMessage; value: string }> = ({ label, value }) => (
    <option value={value}>{__string(label)}</option>
);

type GetOptionValues = <T>(options: ReadonlyArray<SelectOption<T>>, serialize?: (value: T) => string) => string[];
const getOptionValues: GetOptionValues = (options, serialize) => {
    const values = options.map((o) => o.value);
    return serialize
        ? values.map(serialize)
        : values.map((v, i) => (v === undefined ? '' : typeof v === 'string' ? v : String(i)));
};

export const Select: <T>(p: SelectProps<T>) => ReactElement = ({
    value,
    serialize,
    label,
    error,
    required,
    className,
    onChange,
    onBlur,
    options,
    disabled,
    name,
}) => {
    const optionValues = getOptionValues(options, serialize);
    const handlerRemapped = (handler?: typeof onChange) => (optionValue: string) =>
        handler && handler(options[optionValues.indexOf(optionValue)].value);
    const currentValue = options.find((option) => option.value === value);
    const currentIndex = currentValue ? options.indexOf(currentValue) : 0;

    return (
        <div
            className={Util.classNames(
                className,
                required && 'required',
                !value && 'unselected',
                error && 'has-error'
            )}>
            {label && <label>{__(label)}</label>}
            <select
                className="custom-select"
                name={name}
                disabled={disabled}
                onChange={EventUtil.inputHandler(handlerRemapped(onChange))}
                onBlur={EventUtil.inputHandler(handlerRemapped(onBlur))}
                value={optionValues[currentIndex]}>
                {options.map((option, index: number) => (
                    <Option label={option.label} value={optionValues[index]} key={index} />
                ))}
            </select>
            {error && <DisplayError error={error} />}
        </div>
    );
};

type FormSelectProps<T> = { Field: iField<T> } & CommonSelectProps<T>;

export const FormSelect: <T>(p: FormSelectProps<T>) => React.ReactElement<FormSelectProps<T>> = ({
    Field,
    className = 'form-control',
    ...rest
}) => (
    <Field>
        {({ onChange, onBlur, required, value, error }) => (
            <Select {...{ value, error, required, onBlur, onChange, className }} {...rest} />
        )}
    </Field>
);
