import { Persistore } from 'persistore';
import { SpadeIsTrumpStore } from './spadeIsTrump-store';
import { GameState } from './players-store';

const _key = 'SIT.history';

const getHistory = (): GameState[] => {
    try {
        const players = Persistore.get(_key);
        return JSON.parse(players as any);
    } catch (e) {
        return [];
    }
};

const _set = (gameState: GameState): void => {
    const history = getHistory();
    history.unshift(gameState);
    if (history.length > 10) history.pop();
    Persistore.set(_key, JSON.stringify(history));
};

const set = (update: GameState | ((gameState: GameState) => GameState)) => {
    if (typeof update === 'function') {
        SpadeIsTrumpStore.set(({ gameState }) => ({ gameState: update(gameState) }));
    } else {
        SpadeIsTrumpStore.set({ gameState: update });
    }
    _set(SpadeIsTrumpStore.get().gameState);
};

const reset = () => HistoryStore.set([]);

const undo = (): void => {
    const history = getHistory();
    if (history.length > 1) history.shift();
    Persistore.set(_key, JSON.stringify(history));
    SpadeIsTrumpStore.set({ gameState: history[0] });
};

const _clear = (state: GameState): GameState =>
    state.map((player) => ({
        ...player,
        total: 0,
        points: player.points.map(() => ({})),
    }));

const rearrange = () =>
    set((state) => {
        const cleared = _clear(state);
        return [...cleared.slice(1), cleared[0]];
    });

const get = (): GameState => getHistory()[0] || [];

SpadeIsTrumpStore.set({ gameState: get() });

export const HistoryStore = { set, get, undo, reset, rearrange };
