import React, { useCallback } from 'react';
import { Player, PlayerStore } from './players-store';
import { Icon } from '../../components/icons/icon';

export const PlayerRow: React.FC<{ player: Player; pos: number }> = ({ player, pos }) => {
    const { name, shortcut } = player;

    const moveUp = useCallback(() => PlayerStore.moveUp(pos), [pos]);
    const moveDown = useCallback(() => PlayerStore.moveDown(pos), [pos]);
    const remove = useCallback(() => PlayerStore.remove(player), [player]);

    return (
        <tr className="player-row">
            <td className="player-pos">
                <span className="player-move-down" onClick={moveDown}>
                    <Icon.ArrowRight />
                </span>
                <span className="player-current-pos">{pos + 1}</span>
                <span className="player-move-up" onClick={moveUp}>
                    <Icon.ArrowRight />
                </span>
            </td>
            <td className="player-name">{name}</td>
            <td className="player-shortcut">{shortcut}</td>
            <td className="player-actions">
                <span className="player-remove" onClick={remove}>
                    <Icon.Trash />
                </span>
            </td>
        </tr>
    );
};
