import React, { useCallback, MouseEvent } from 'react';
import { createStore } from 'react-use-sub';
import { UrlUtil } from './util/url';
import { App } from './App';
import { Again } from './games/Again';
import { MissionControl } from './mission/MissionControl';
import { SpadeIsTrump } from './games/SpadeIsTrump';
import { VeryClever } from './games/VeryClever';
import { Decrypto } from './games/Decrypto';

export enum NavLoc {
    AGAIN = 'again',
    DECRYPTO = 'decrypto',
    MISSION = 'mission',
    SIT = 'spade-is-trump',
    VERY_CLEVER = 'very-clever',
}
const _getCurrentNavLoc = (): NavLoc | null => {
    const { loc } = UrlUtil.locationSearchToQuery(window.location.search);
    return Object.values(NavLoc).includes(loc) ? loc : null;
};

const [useNavSub, NavStore] = createStore<{ loc: NavLoc | null }>({ loc: _getCurrentNavLoc() });

const Root: React.FC = () => {
    const loc = useNavSub(({ loc }) => loc);

    if (loc === NavLoc.AGAIN) return <Again />;
    if (loc === NavLoc.DECRYPTO) return <Decrypto />;
    if (loc === NavLoc.MISSION) return <MissionControl />;
    if (loc === NavLoc.SIT) return <SpadeIsTrump />;
    if (loc === NavLoc.VERY_CLEVER) return <VeryClever />;

    return <App />;
};

const onLocationChange = () => {
    const loc = _getCurrentNavLoc();
    if (loc !== NavStore.get().loc) NavStore.set({ loc });
};
window.addEventListener('popstate', onLocationChange);

const Link: React.FC<{ children: React.ReactNode; loc: NavLoc }> = ({ children, loc }) => {
    const href = '?loc=' + loc;

    const onClick = useCallback(
        (event: MouseEvent<HTMLAnchorElement>) => {
            event.preventDefault();
            NavStore.set({ loc });
            window.history.pushState(null, loc, href);
        },
        [loc, href]
    );

    return (
        <a href={href} onClick={onClick}>
            {children}
        </a>
    );
};

export const AppNavigation = { Root, Link };
