import React, { useState } from 'react';
import { FormInput } from '../../components/form/input';
import { FormData, Morfi } from 'morfi';
import { Code } from './Decrypto-store';
import { Icon } from '../../components/icons/icon';
import { __ } from 'first-intl';

type CodeWords = { first: string; second: string; third: string };
const initialCodeWords = { first: '', second: '', third: '' };
const initialCodeWordsData = { values: initialCodeWords, errors: {} };
const { Form, Fields } = Morfi.create<CodeWords>(initialCodeWords);
const codeWordValidator = (v?: string) => {
    if (!v || !v.trim()) return { id: 'DECRYPTO.codeWord.required' };
    try {
        btoa(v);
    } catch (e) {
        return { id: 'DECRYPTO.codeWord.invalid' };
    }
};
const codeWordValidation = {
    first: { onChange: codeWordValidator },
    second: { onChange: codeWordValidator },
    third: { onChange: codeWordValidator },
};

export type CodeWordsSelectionProps = { onSubmit: (codeWords: CodeWords) => void; code?: Code };

export const CodeWordsSelection: React.FC<CodeWordsSelectionProps> = ({ onSubmit, code }) => {
    const [codeWordsData, setCodeWordsData] = useState<FormData<CodeWords>>(initialCodeWordsData);
    return (
        <div className="code-words-selection">
            {code && (
                <div className="code">
                    <span>{code.join('.')}</span>
                </div>
            )}
            <Form onChange={setCodeWordsData} data={codeWordsData} onSubmit={onSubmit} validation={codeWordValidation}>
                <FormInput Field={Fields.first} />
                <FormInput Field={Fields.second} />
                <FormInput Field={Fields.third} />
                <button type="submit" disabled={Morfi.hasErrors(codeWordsData)}>
                    <Icon.Mask />
                    <div className="description">{__('DECRYPTO.btn.submit')}</div>
                </button>
            </Form>
        </div>
    );
};
